import React, { useEffect, useContext } from "react";
import useAxios from "axios-hooks";
import {
  CardContent,
  createStyles,
  makeStyles,
  Theme,
  Grid,
  Typography,
  useMediaQuery
} from "@material-ui/core";
import { pluck, isEmpty, without, pathOr } from "ramda";
import { If, Then } from "react-if";

import { FullscreenPaper } from "../../bos_common/src/components/Papers";
import MerchandiseListing from "../../bos_common/src/components/Merchandise/MerchandiseListing";
import { groupMerchandiseDataByCategories } from "../../bos_common/src/services/merchandiseUtils";
import {
  Merchandise,
  Merchant,
  MerchandiseApiResponseType,
  MerchandiseModifier,
  MerchantCoupon,
  ItemType,
} from "../../services/models";

import MenuItemCard from "./MenuItemCard";
import { MERCHANT_DATA_FETCH_INTERVAL } from "../../config/constants";
import { useFetchApiOnInterval } from "../../bos_common/src/hooks";
import SimpleLoader from "../../bos_common/src/components/SimpleLoader";
import { VisitedMerchantContext } from "../../context/VisitedMerchantContext/VisitedMerchantContext";
import { isMerchandiseAvailable } from "../../utils";
import MarketingOneLiner from "../common/MarketingOneLiner";
import CountBox from "../common/CountBox";
import PromotionButton from "../Promotions/PromotionButton";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      position: "relative",
      backgroundColor: theme.palette.background.paper,
      flex: "1 1 auto",

      "& .category-info": {
        marginBottom: theme.spacing(2),
        marginTop: theme.spacing(2),
      },
      "&:last-child": {
        minHeight: "calc(100vh - 150px)",
      },
    },
  })
);

interface RenderMerchandiseProps {
  merchandises: Merchandise[];
  merchant: Merchant;
  modifiers: MerchandiseModifier[] | undefined;
}

const RenderMerchandises = ({
  merchandises = [],
  merchant,
  modifiers,
}: RenderMerchandiseProps) => {

  const promotion = pathOr({}, ['promotions', 0], merchant);
  const coupon = pathOr({}, ['coupons', 0], promotion);

  // sort by available so that unavailable items are listed at the end and filter visible merchandises
  merchandises
    ?.sort((a: Merchandise, b: Merchandise) => {
      if (a.inventory?.dailyCurrentStock === 0) return 1;
      if (b.inventory?.dailyCurrentStock === 0) return -1;

      return (a.displayOrder !== b.displayOrder)
        ? (a.displayOrder > b.displayOrder ? 1 : -1)
        : (a.name > b.name ? 1 : -1 );
    })
    .filter((item) => item.visible);
  return (
    <If condition={merchandises.length > 0}>
      <Then>
        {merchandises.map((merchandise: Merchandise) => (
          <Grid item xs={12} sm={6} lg={4} key={merchandise.id}>
            <MenuItemCard
              merchandise={merchandise}
              merchant={merchant}
              modifiers={modifiers}
              coupon={coupon}
            />
          </Grid>
        ))}
      </Then>
    </If>
  );
};

const RenderFeaturedDeals = ({merchandises, merchant, modifiers} : RenderMerchandiseProps) => {

  const classes = useStyles();
  const promotion = pathOr({}, ['promotions', 0], merchant);
  const coupon: MerchantCoupon = pathOr({}, ['coupons', 0], promotion);

  const featuredDealItems = React.useMemo(() => {
    if (coupon && coupon.affectedItems) {
      return merchandises.filter((m) => {
        if (!isMerchandiseAvailable(m)) return false; // keep only available items
        const match = coupon.affectedItems?.filter((item) => {
          return (item.type === ItemType.CATEGORY) ? (Number(item.id) === m.categoryId) : (item.id === m.id);
        });
        return match && (match?.length > 0);
      })
    }
    return [];
  }, [merchant, merchandises]);

  if (featuredDealItems.length < 1)
    return null;

  return (
    <CardContent className={classes.root}>
      <div className="category-info">
        <Typography variant="body1" component="div" display='flex' alignItems='center'>
          Featured Deals&nbsp;&nbsp;
          <CountBox count={featuredDealItems.length} />
        </Typography>
        <div style={{display: 'inline-block', marginTop: '8px', fontSize: '12px'}}>
          <MarketingOneLiner description={promotion?.oneliner} />
        </div>
      </div>
      <Grid container spacing={2}>
        <RenderMerchandises
          merchandises={featuredDealItems}
          modifiers={modifiers}
          merchant={merchant}
        />
      </Grid>
    </CardContent>
  )
}

type MerchantMenuProps = {
  merchant: Merchant;
};
const MerchantMenu = (props: MerchantMenuProps): React.ReactElement => {
  const { merchant } = props;
  const { setVisitedMenu } = useContext(VisitedMerchantContext);

  const [{ data: merchandiseData, loading }, fetchMerchandises] =
    useAxios<MerchandiseApiResponseType>({
      url: "/merchandises",
      params: { merchantId: merchant.id },
    });
  useFetchApiOnInterval(fetchMerchandises, MERCHANT_DATA_FETCH_INTERVAL, true);

  const { mercCategories = [], merchandises = [] } = merchandiseData || {};

  useEffect(() => {
    merchandiseData && setVisitedMenu(merchandiseData);
  }, [merchandiseData])

  const classes = useStyles();
  const isMobile = useMediaQuery((theme: Theme) => theme.breakpoints.down('sm'));

  const groupedMerchandises = groupMerchandiseDataByCategories({
    categories: mercCategories,
    merchandises: merchandises,
    merchant,
  });
  const categories = without([null], pluck('category', groupedMerchandises));

  return (
    <FullscreenPaper>
      <SimpleLoader loading={loading} />
      <MerchandiseListing
        categories={categories}
        menuItemsAvailable={!isEmpty(merchandises)}
        loading={loading}
      >
        <>
          <If condition={isMobile}>
            <PromotionButton merchant={merchant} />
          </If>
          <RenderFeaturedDeals
            merchant={merchant}
            merchandises={merchandises}
            modifiers={merchandiseData?.modifiers} />
          {groupedMerchandises?.map(
            ({ category, merchandises }, idx: number) => (
              <CardContent
                className={classes.root}
                key={idx}
                id={`category-${category ? category.id : "0"}`}
              >
                {category && (
                  <div className="category-info">
                    {
                      <Typography variant="body1" component="div" display='flex' alignItems='center'>
                        {category.name}&nbsp;&nbsp;
                        <CountBox count={merchandises.length} />
                      </Typography>
                    }
                    {category.description && (
                      <Typography
                        variant="subtitle2"
                        component="p"
                        color="textSecondary"
                      >
                        {category.description}
                      </Typography>
                    )}
                  </div>
                )}

                <Grid container spacing={2}>
                  <RenderMerchandises
                    merchandises={merchandises}
                    modifiers={merchandiseData?.modifiers}
                    merchant={merchant}
                  />
                </Grid>
              </CardContent>
            )
          )}
        </>
      </MerchandiseListing>
    </FullscreenPaper>
  );
};

export default MerchantMenu;
