import React from 'react';
import { makeStyles, createStyles, Theme, LinearProgress } from '@material-ui/core';
import { Else, If, Then } from 'react-if';

const useStyles = makeStyles((theme: Theme) =>
	createStyles({
		root: {
			height: 4,
			width: '100%',
		},
	}),
)

type SimpleLoaderProps = {
	loading: boolean,
	backgroundColor?: string
}

const SimpleLoader = ({ loading = false, backgroundColor = 'transparent' }: SimpleLoaderProps) => {
	const classes = useStyles()

	return (
		<If condition={loading}>
			<Then>
				<LinearProgress sx={{ zIndex: 10 }} />
			</Then>
			<Else>
				<div className={classes.root} style={{ backgroundColor }}/>
			</Else>
		</If>
	)
}

export default SimpleLoader