import React from 'react';
import assert from 'assert';
import ReactGA from 'react-ga'

import config from '../config/config';
import { isEmptyOrNil, isProduction } from '../utils';
import { Merchandise, Merchant, Order, User } from './models';

type CapturedEventData = {
  merchant?: Merchant,
  user?: User|null,
  order?: Order,
  merchandise?: Merchandise,
  other?: { [key: string]: any }
}

export enum EVENT_TYPES {
  // page visit events
  VISITED_MERCHANT_MENU = "VISITED_MERCHANT_MENU",
  VISITED_MERCHANDISE_PAGE = "VISITED_MERCHANDISE_PAGE",
  VISITED_SHOPPING_CART = "VISITED_SHOPPING_CART",
  VISITED_1M_POINTS = 'VISITED_1M_POINTS',
  VISITED_ORDER_STATUS = 'VISITED_ORDER_STATUS',

  // order checkout events
  OPEN_CHECK_ORDER_STARTED = "OPEN_CHECK_ORDER_STARTED",
  OPEN_CHECK_ORDER_COMPLETED = "OPEN_CHECK_ORDER_COMPLETED",

  ORDER_CHECKOUT_STARTED = "ORDER_CHECKOUT_STARTED",
  ORDER_CHECKOUT_CANCELLED = "ORDER_CHECKOUT_CANCELLED",
  ORDER_CHECKOUT_COMPLETED = "ORDER_CHECKOUT_COMPLETED",

  ORDER_1M_POINTS = "ORDER_1M_POINTS"
}

export enum EVENT_ACTIONS {
  CLICK_ACTION = 'CLICK_ACTION',
  ON_PAGE_LOAD = 'ON_PAGE_LOAD',
  ON_POPUP_OPEN = 'ON_POPUP_OPEN',
  SWITCHED_MERCHANT = 'SWITCHED_MERCHANT'
}

const GA_OPTIONS = {
  siteSpeedSampleRate: 100
}

export const initializeReactGA = () => {
  // load tracking ID from the config file
  if (!isEmptyOrNil(config.appConfig.gaTrackingId) && isProduction()) {
    ReactGA.initialize(
      config.appConfig.gaTrackingId,
      { gaOptions: GA_OPTIONS }
    );
    const pageView = window.location.pathname + window.location.search
    ReactGA.pageview(pageView)
  }
}

const getUserDimension = (data: CapturedEventData): string | undefined => {
  if (!data.user) return undefined

  const { id, email, phoneNumber } = data.user
  const _id = id ? `id=${id};` : ''
  const _email = email ? `email=${email};` : ''
  const _phone = phoneNumber ? `phoneNumber=${phoneNumber};` : ''
  return `${_id}${_email}${_phone};type=user`
}

const getMerchantDimension = (data: CapturedEventData): string | undefined => {
  if (!data.merchant) return undefined
  const { username, id } = data.merchant
  return `id=${id};username=${username};type=merchant`
}

const getOrderDimension = (data: CapturedEventData): string | undefined => {
  if (!data.order) return undefined
  const { id, merchantId } = data.order
  return `id=${id};merchantId=${merchantId};type=order`
}

const getMerchandiseDimension = (data: CapturedEventData): string | undefined => {
  if (!data.merchandise) return undefined
  const { id, merchantId, name } = data.merchandise
  return `id=${id};name=${name};merchantId=${merchantId};type=merchandise`
}

const sendEventData = (eventData: ReactGA.EventArgs) => {
  try {
    if (!isProduction()) {
      console.log("--------------")
      console.log("EVENT Captured")
      console.log(eventData)
      console.log("--------------")
      return;
    }

    ReactGA.event(eventData)

  } catch (error) {
    console.error("sendEventData :error", error)
  }
}

interface CaptureEventParams {
  category: EVENT_TYPES,
  action: EVENT_ACTIONS | string,
  data?: CapturedEventData,
  label?: string
}

export const captureSwitchMerchantEvent = (data: CapturedEventData) => {
  const eventData: ReactGA.EventArgs = {
    category: EVENT_TYPES.VISITED_MERCHANT_MENU,
    action: EVENT_ACTIONS.SWITCHED_MERCHANT,
    dimension1: getMerchantDimension(data),
    dimension2: getUserDimension(data),
  }

  sendEventData(eventData);
}

export const captureClickEvent = (params: Omit<CaptureEventParams, 'action'>) => {
  const { category, data = {}, label = '' } = params;

  const eventData = {
    category,
    action: EVENT_ACTIONS.CLICK_ACTION,
    label,
    dimension1: getMerchantDimension(data),
    dimension2: getUserDimension(data),
    dimension3: getOrderDimension(data),
    dimension4: getMerchandiseDimension(data),
  }
  sendEventData(eventData);
}

export const captureOrderCheckoutCompleteEvent = (data: CapturedEventData) => {
  const eventData: ReactGA.EventArgs = {
    category: EVENT_TYPES.ORDER_CHECKOUT_COMPLETED,
    action: EVENT_ACTIONS.ON_PAGE_LOAD,
    dimension1: getMerchantDimension(data),
    dimension2: getUserDimension(data),
    dimension3: getOrderDimension(data),
  }
  sendEventData(eventData);
}