import { If, Then } from 'react-if';
import { pipe, propOr, split, without, slice } from 'ramda';
import { CardContent, makeStyles, Typography, createStyles, Theme, Card, CardActionArea, Box, Chip, useTheme, Divider, Stack, styled, } from '@material-ui/core';

// src
