import React, { SyntheticEvent, useContext, useEffect, useState } from 'react';
import { If } from 'react-if';
import { Card, CardContent, Divider, FormControl, FormControlLabel, FormLabel, Radio, RadioGroup, Typography } from '@material-ui/core';

import QuantityInput from '../MerchantMenu/QuantityInput';
import FormGroupCardWrapper from '../common/FormGroupCardWrapper';
import { AppContext, CartItemType } from '../../context/AppContext';
import { Merchandise } from '../../services/models';
import renderPrice from '../../bos_common/src/components/Price';

export interface QuantityChangeParams {
  count: number;
  change: number;
}

type MerchandiseNullable = Merchandise | null | undefined;
type BagsAddition = 'with' | 'without';

export const CartBagItemCart = (): JSX.Element | null => {
  const { merchantConfig, cart, addItemToCart, removeItemFromCart } = useContext(AppContext)
  const [bagsCount, setBagsCount] = useState<number>(0);
  const [merchantCarryoutBag, setMerchantCarryoutBag] = useState<MerchandiseNullable>(null);

  useEffect(() => {
    let carryoutBagInCart: CartItemType | undefined;
    const { carryoutBag } = merchantConfig?.merchant.orderingConfig || {};
    setMerchantCarryoutBag(carryoutBag);

    if (!carryoutBag) {
      return;
    }

    // eslint-disable-next-line prefer-const
    carryoutBagInCart = cart.find((item) => item.id === carryoutBag.id);
    setBagsCount(carryoutBagInCart?.quantity || 0);
  }, [merchantConfig, cart]);

  if (!merchantCarryoutBag) {
    return null;
  }

  const onChangeBagsQuantity = ({ count, change }: QuantityChangeParams) => {
    if (!merchantCarryoutBag || change === 0) {
      return;
    }

    // eslint-disable-next-line prefer-const
    setBagsCount(count);

    if (change > 0) {
      const newItem: CartItemType = { ...merchantCarryoutBag, quantity: change };
      addItemToCart(newItem, merchantConfig?.merchant);
    } else if (change < 0) {
      for (let i = change; i < 0; i++) {
        removeItemFromCart({ merchandiseId: merchantCarryoutBag.id, modifiersSignature: undefined });
      }
    }
  }

  const handleChangeBags = (e: SyntheticEvent) => {
    const bagsAddition: BagsAddition = (e.target as HTMLInputElement).value as BagsAddition;
    if (bagsAddition === 'with') {
      onChangeBagsQuantity({ count: 1, change: 1 - bagsCount });
    } else {
      onChangeBagsQuantity({ count: 0, change: -bagsCount });
    }
  }

  return (
    <Card elevation={3}>
      <div style={{padding: '0px 8px '}}>
        <FormControl variant="standard" fullWidth>
          <FormLabel color="primary" style={{ padding: '8px 0px' }}>Bags?</FormLabel>
          <Divider />
          <RadioGroup
            aria-label="Carry out bags"
            name="radio-buttons-group"
            value={bagsCount === 0 ? 'without' : 'with'}
            onChange={handleChangeBags}
          >
            <FormControlLabel value="without" control={<Radio color="primary" />} label="No"  />
            <Divider />
            <FormControlLabel value="with" control={<Radio color="primary"  />} label="Yes" />
            <If condition={bagsCount > 0}>
              <div style={{ paddingBottom: 8, display:'flex', justifyContent:'space-between'}}>
                <QuantityInput
                  quantity={bagsCount}
                  minQuantity={0}
                  onQuantityChange={(count, change) => onChangeBagsQuantity({ count, change })}
                />
                <Typography variant="body2" component="div">
                  {renderPrice(merchantCarryoutBag.price * bagsCount)}
                </Typography>
              </div>
            </If>
          </RadioGroup>
        </FormControl>
      </div>
    </Card>
  );
}
