import { isAfter } from "date-fns";
import { Merchandise } from "../types/MerchandiseType";
import { CouponValueType, ItemType, MerchantCoupon } from "../types/MerchantCouponType";

export const isCouponValid = (coupon?: MerchantCoupon): boolean => {
  if (!coupon || !coupon.live) {
    return false;
  }

  if (coupon.limit && coupon.usedCount > coupon.limit) {
    return false;
  }

  if (coupon.valueType === CouponValueType.PERCENTAGE && coupon.value > 1) {
    return false;
  }

  const now = new Date();
  if (now < coupon.startDate || now > coupon.endDate) {
    return false;
  }

  return true;
};

export const isCouponValidOnMerchandise = (merchandise: Merchandise, coupon?: MerchantCoupon) => {
  const result = isCouponValidOnMerchandises([merchandise], coupon);
  return (result && result.length > 0);
}


export const isCouponValidOnMerchandises = (merchandises: Merchandise[], coupon?: MerchantCoupon) => {
  if (!coupon || !isCouponValid(coupon)) {
    return null;
  }

  if (coupon.affectedItems) {
    const eligibleItems: Merchandise[] = merchandises.filter((item: Merchandise) => {
      const match = coupon.affectedItems?.filter((aitem) => {
        if (aitem.type === ItemType.CATEGORY) {
          return aitem.id === item.categoryId;
        } else if (aitem.type == ItemType.MERCHANDISE) {
          return aitem.id === item.id;
        }
        return false;
      });
      return (match?.length || 0) > 0;
    });
    return eligibleItems;
  }

  return merchandises;
}

export const isValidExpiryDate = (endDate: Date) => {
  if (!endDate) return false;

  const today = new Date();
  const _endDate = new Date(endDate)
  return _endDate.getFullYear() <= 9000 && isAfter(_endDate, today)
}
