import React, { useContext, useMemo } from "react";
import { useHistory } from "react-router";
import { Alert, CardContent, createStyles, makeStyles } from "@material-ui/core";
import { If, Then, Else } from 'react-if';

import { ColoredPaper } from '../bos_common/src/components/Papers';
import { AppContext } from "../context/AppContext";
import { Order, OrderType } from "../services/models";
import { getTotalItems, getTotalPrice } from "../services/cartUtils";

import PageHeader from "../components/common/PageHeader";
import StripePaymentForm from "../components/Payment/StripePaymentForm";
import NoPaymentForm from "../components/Payment/NoPaymentForm";
import BackButton from "../components/common/BackButton";
import { isDineInPaymentDisabled } from "../utils";
import { loadStripe } from "@stripe/stripe-js";
import { Elements } from "@stripe/react-stripe-js";

const useStyles = makeStyles(() =>
  createStyles({
    root: {
      '& .content': {
        paddingTop: 0,
      }
    },
  }),
);

const CheckoutPage = (): React.ReactElement => {
  const history = useHistory();
  const classes = useStyles();
  const { cart, clearCart, merchantConfig, tip, coupon, orderType } = useContext(AppContext);

  const count = getTotalItems(cart)
  const amount = getTotalPrice({ cart, tip, coupon, orderingConfig: merchantConfig?.merchant.orderingConfig })
  const isStripePaymentAcceptable = amount >= 0.50
  const isPaymentDisabled = (orderType === OrderType.DINEIN) && isDineInPaymentDisabled(merchantConfig?.merchant);

  let stripePublishedKey = 'pk_test_51IotjOIJSYDtijmKqRuxe97OR3doGxkozedGxdULMUlmtu0ehEa5mhAoGU89Ncnzr324lwCHoU5EoEG9ZBFJnTEv00lAYXcUwB'
  let stripeAccount = merchantConfig?.merchant.stripe?.stripeTestAccountId
  if (merchantConfig?.merchant.production === true) {
    stripePublishedKey = 'pk_live_51IotjOIJSYDtijmKrOPBFfTy72gs124DsB5JsD4KTVGKB65pR1TZwu4Bqbew2l2wfL6g9UMDGaTIboEbgipjKqlo00XfpgRZWA'
    stripeAccount = merchantConfig?.merchant.stripe?.stripeAccountId
  }

  const stripePromise = useMemo(() => loadStripe(stripePublishedKey), []);

  const onSuccessPayment = (order: Order) => {
    // clear cart
    clearCart();

    history.push({
      pathname: `/order/${order.id}`,
      state: { new: true }
    });
  }

  let stripeTitle = "Payment (test mode)"
  if (merchantConfig?.merchant.production === true) {
    stripeTitle = "Payment"
  }
  const title = isStripePaymentAcceptable ? stripeTitle : "Checkout"

  return (
    <div className="container">
      <PageHeader leftChild={<BackButton />} title={title} />
      <ColoredPaper variant="outlined" className={classes.root}>
        <CardContent className={"content"}>
          <If condition={count === 0}>
            <Then>
              <Alert severity="info">
                {"You don't have any item in your cart"}
              </Alert>
            </Then>
            <Else>
              <If condition={isStripePaymentAcceptable}>
                <Then>
                  <If condition={isPaymentDisabled}>
                    <Then>
                      <Alert severity="warning">
                        {"Online payment is not supported at this store. Please checkout at the counter or ask for paper check."}
                      </Alert>
                    </Then>
                    <Else>
                      <Elements stripe={stripePromise}>
                        <StripePaymentForm onSuccessSubmit={onSuccessPayment} />
                      </Elements>
                    </Else>
                  </If>
                </Then>
                <Else>
                  <NoPaymentForm onSuccessSubmit={onSuccessPayment} />
                </Else>
              </If>
            </Else>
          </If>
        </CardContent>
      </ColoredPaper>
    </div>
  )

}

export default CheckoutPage