import React from 'react';
import { filter, flatten, map, pathEq, pathOr, pipe, pluck } from "ramda";
import { If, Then, Else } from 'react-if';

import { ClassifiedDetailsType, ClassifiedEntry, ClassifiedKey, ClassifiedType } from "../../bos_common/src/types/ClassifiedType";
import { MerchantPromotion } from '../../bos_common/src/types/MerchantPromotionType';
import { Merchant, User } from '../../services/models';
import { isEmptyOrNil, isUserEligibleForPromotion } from "../../utils";
import { CarousellItemWrapper } from './styledComponents';
import ClassifiedMerchantCard from './ClassifiedMerchantCard';
import ClassifiedPromotionCard from './ClassifiedPromotionCard';
import ClassifiedMerchandiseCard from './ClassifiedMerchandiseCard';
import { isPromotionValid } from '../../bos_common/src/services/PromotionUtils';

export const isClassifiedForMerchant = (entry: ClassifiedEntry) => {
  return entry.type === ClassifiedType.merchant && !isEmptyOrNil(entry.merchant)
}

export const isClassifiedForMerchandise = (entry: ClassifiedEntry) => {
  return entry.type === ClassifiedType.merchandise && !isEmptyOrNil(entry.merchandise)
}

export const isClassifiedForPromotion = (entry: ClassifiedEntry) => {
  return pathEq(['details', 'key'], ClassifiedKey.hotDeal, entry)
    && entry.type === ClassifiedType.merchant
    && !isEmptyOrNil(entry.merchant)
    && !isEmptyOrNil(pathOr([], ['merchant', 'promotions'], entry))
}

export const isBigSavingsClassified = (entry: ClassifiedEntry) => {
  return entry.details.key === ClassifiedKey.bigSavings && !isEmptyOrNil(entry.value)
}

export const getBigSavingsValue = (entry: ClassifiedEntry) => {
  const { value, valueType } = entry
  const isPercentageType = valueType == 'percentage'

  return `${value * (isPercentageType ? 100 : 1)}${isPercentageType ? '%' : ''}`
}

export const getClassifiedItemsElementsList = (itemsList: ClassifiedEntry[]) => {
  return itemsList.map((entry: ClassifiedEntry, j) => {
    const { merchant, merchandise } = entry;

    if (isClassifiedForMerchant(entry) && merchant) {
      return (
        <CarousellItemWrapper key={merchant.id}>
          <ClassifiedMerchantCard entry={entry} />
        </CarousellItemWrapper>
      )
    }

    if (isClassifiedForMerchandise(entry) && merchandise && merchant) {
      return (
        <CarousellItemWrapper key={merchant.id}>
          <ClassifiedMerchandiseCard merchant={merchant!} merchandise={merchandise} />
        </CarousellItemWrapper>
      )
    }
  });
}

export type FormattedPromotionsList = Array<{ merchant: Merchant, promotions: MerchantPromotion[] }>
export const getPromotionsList = (itemsList: ClassifiedEntry[], user?: User): FormattedPromotionsList => {
  return map(
    (data: ClassifiedEntry) => ({
      merchant: data.merchant,
      promotions: filter((p: MerchantPromotion) => {
        return isPromotionValid(p) && isUserEligibleForPromotion(p, user)
      }, pathOr([], ['merchant', 'promotions'], data)),
    }),
    itemsList
  )
}

export const getPromotionsElementsList = (itemsList: ClassifiedEntry[], user: User | undefined, includeWrapper = true) => {
  const promotionsList = getPromotionsList(itemsList, user)

  let promotionCardElements: JSX.Element[] = []

  promotionsList.forEach((data: { merchant: Merchant, promotions: MerchantPromotion[] }) => {
    const { merchant, promotions } = data
    if (isEmptyOrNil(promotions)) return;

    const thisPromotionCardsList = promotions.map((promotion: MerchantPromotion) => (
      <If condition={includeWrapper} key={`${merchant.id}_${promotion.id}`}>
        <Then>
          <CarousellItemWrapper key={`${merchant.id}_${promotion.id}`}>
            <ClassifiedPromotionCard
              key={`${merchant.id}_${promotion.id}`}
              merchant={merchant}
              promotion={promotion}
            />
          </CarousellItemWrapper>
        </Then>
        <Else>
          <ClassifiedPromotionCard
            key={`${merchant.id}_${promotion.id}`}
            merchant={merchant}
            promotion={promotion}
          />
        </Else>
      </If>
    ))
    promotionCardElements = [...promotionCardElements, ...thisPromotionCardsList]
  })

  return promotionCardElements;
}