import React, { useContext, useMemo } from 'react';
import { CardContent, createStyles, Grid, makeStyles, Theme, Typography } from '@material-ui/core';
import { pathOr } from 'ramda';
import { If } from 'react-if';
import LazyLoad from 'react-lazyload';

import { ClassifiedDetailsType, ClassifiedEntry, ClassifiedKey } from '../../bos_common/src/types/ClassifiedType';

import { isEmptyOrNil } from '../../utils';
import { UserContext } from '../../bos_common/src/context/UserContext';
import { getPromotionsElementsList, isClassifiedForMerchandise, isClassifiedForMerchant } from './utils';
import ClassifiedMerchantCard from './ClassifiedMerchantCard';
import ClassifiedMerchandiseCard from './ClassifiedMerchandiseCard';
import CountBox from '../common/CountBox';
import BottomDrawer from '../../bos_common/src/components/BottomDrawer';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {

      '&.MuiCardContent-root': {
        height: '70vh',
        overflow: 'auto',

        [theme.breakpoints.up('sm')]: {
          height: '65vh'
        },
      },

      '& .drawerContent': {
        height: '70vh',
        paddingTop: 0,

        [theme.breakpoints.up('sm')]: {
          height: '65vh'
        },
      },

      '& .MuiCard-root': {
        borderRadius: '20px',
        width: '100%',

        '&:before': {
          display: 'none'
        }
      }
    }
  }),
);

interface ClassifiedDrawerProps {
  open: boolean,
  setDrawerOpen: (o: boolean) => void,
  list: ClassifiedEntry[]
}
const ClassfiedDrawer = (props: ClassifiedDrawerProps) => {
  const { open, list, setDrawerOpen } = props;
  const classes = useStyles();
  const classified: ClassifiedDetailsType = pathOr({}, [0, 'details'], list);

  const { user } = useContext(UserContext)

  const isHotDealClassified = classified.key === ClassifiedKey.hotDeal;

  const promotionElementsList = useMemo(() => {
    return isHotDealClassified ? getPromotionsElementsList(list, user, false) : []
  }, [list, isHotDealClassified]);

  const itemsList = isHotDealClassified ? promotionElementsList : list

  return (
    <BottomDrawer
      open={open}
      setOpen={setDrawerOpen}
      title={
        <>
          <Typography variant="h6" className="drawerTitle" component="div" gutterBottom sx={{ display: 'flex', alignItems: 'center' }}>
            {classified.name}&nbsp;
            <CountBox count={itemsList.length} />
          </Typography>
          <If condition={!isEmptyOrNil(classified.oneliner)}>
            <Typography variant="subtitle1" color="textSecondary" className="drawerSubtitle" component="div">
              {classified.oneliner}
            </Typography>
          </If>
        </>
      }
    >
      <CardContent className={classes.root}>
        <Grid container spacing={3}>
          {
            itemsList.map((entry: any, i: number) => {
              let item;

              if (isHotDealClassified) {
                item = entry
              } else {
                const { merchant, merchandise } = entry as ClassifiedEntry;

                if (isClassifiedForMerchant(entry as ClassifiedEntry) && merchant) {
                  item = <ClassifiedMerchantCard entry={entry as ClassifiedEntry} />
                } else if (isClassifiedForMerchandise(entry as ClassifiedEntry) && merchandise && merchant) {
                  item = <ClassifiedMerchandiseCard merchant={merchant!} merchandise={merchandise} />
                }
              }

              return (
                <Grid item xs={12} sm={6} md={3} key={`row-item-${i}`}>
                  <LazyLoad overflow height={100} unmountIfInvisible>
                    {item}
                  </LazyLoad>
                </Grid>
              )
            })
          }
        </Grid>
      </CardContent>
    </BottomDrawer>
  )
}

export default ClassfiedDrawer;