import { MerchandiseModifier } from "../types/MerchandiseType"

export const toModifersMap = (modifiers: MerchandiseModifier[]|undefined) => {
  // creates modifier name to modifier map
  const toModifiers: { [key: string]: MerchandiseModifier } = {}
  modifiers?.map((modifier) => {
    toModifiers[modifier.name] = modifier
  })
  return toModifiers
}
