import React, { useCallback, useEffect, useMemo } from "react";
import {
  FormControl, InputLabel, Select, MenuItem, Checkbox, FormControlLabel,
  FormGroup, FormHelperText, FormLabel, Divider, RadioGroup, Radio
} from "@material-ui/core";
import { pathOr, pick, pipe, pluck, propOr, values } from "ramda";
import { useFormContext, Controller } from "react-hook-form";

import { MerchandiseApiResponseType, MerchandiseModifierCategory, MerchandiseModifier } from "../../services/models";
import renderPrice from '../../bos_common/src/components/Price';
import FormGroupCardWrapper from "../common/FormGroupCardWrapper";
import { isEmptyOrNil } from "../../utils";

interface ModifierMap { [key: number]: MerchandiseModifier; }

const checkSelectionLimitForCategory = (quantity: number | null, categoryModifiersValues: boolean[], defaultReturn: boolean) => {
  if (quantity == null || quantity < 1) return defaultReturn;

  const selectedModifiers = categoryModifiersValues.filter(i => i)
  return selectedModifiers.length >= quantity
}

const getModifierLabel = (m: MerchandiseModifier) => {
  const price = Number(m.price);
  return price === 0 ? m.name : `${m.name} (+ ${renderPrice(price)})`
}

const renderModifierDropDown = (category: MerchandiseModifierCategory, toModifiers: ModifierMap) => {
  const { formState, control } = useFormContext();
  const errors = formState.errors

  const modifiers = useMemo(() => category.modifierIds.map((modifierId) => toModifiers[modifierId]), [category]);
  const htmlId = `category-${category.id}`
  const defaultModifier = modifiers.find((modifier) => modifier.isDefault && modifier.available)
  const helperText = errors[category.name] ? errors[category.name].message : category.description as string | undefined
  const required = category.minQuantity > 0
  const rules = required ? { required: 'required' } : undefined

  return (
    <FormGroupCardWrapper key={category.id} id={category.id} elevation={2}>
      <FormControl
        required={required}
        error={Boolean(errors[category.name])}
        variant="standard"
        fullWidth>
        <InputLabel htmlFor={htmlId}>{category.name}</InputLabel>
        <Controller
          defaultValue={defaultModifier?.name || ''}
          name={`${category.name}` as const}
          control={control}
          rules={rules}
          render={({ field }) =>
            <Select {...field}>
              {modifiers.map((m: MerchandiseModifier) => {
                return (m &&
                  <MenuItem key={m.id} value={m.name}>
                    {getModifierLabel(m)}
                  </MenuItem>
                )
              })}
            </Select>
          }
        />
        {helperText && <FormHelperText>{helperText}</FormHelperText>}
      </FormControl>
    </FormGroupCardWrapper>
  )
}

const renderModifierRadioSelect = (category: MerchandiseModifierCategory, toModifiers: ModifierMap) => {
  const { formState, control } = useFormContext();
  const errors = formState.errors

  const modifiers = useMemo(() => category.modifierIds.map((modifierId) => toModifiers[modifierId]), [category]);
  const defaultModifier = modifiers.find((modifier: MerchandiseModifier) => modifier.isDefault && modifier.available)
  const helperText = errors[category.name] ? errors[category.name].message : category.description as string | undefined
  const required = category.minQuantity > 0
  const rules = required ? { required: 'required' } : undefined

  return (
    <FormGroupCardWrapper key={category.id} id={category.id} elevation={2}>
      <FormControl
        required={required}
        error={Boolean(errors[category.name])}
        variant="standard"
        fullWidth>
        <FormLabel>{category.name}</FormLabel>
        <Divider />
        <Controller
          defaultValue={defaultModifier?.name || ''}
          name={`${category.name}` as const}
          control={control}
          rules={rules}
          render={({ field }) =>
            <RadioGroup aria-label={category.name} {...field}>
              {modifiers.map((m: MerchandiseModifier, index: number) => {
                return (m &&
                  <div key={m.id}>
                    {(index !== 0) && <Divider />}
                    <FormControlLabel value={m.name} control={<Radio color="primary" disabled={!m?.available} />} label={getModifierLabel(m)} />
                  </div>
                )
              })}
            </RadioGroup>
          }
        />
        {helperText && <FormHelperText>{helperText}</FormHelperText>}
      </FormControl>
    </FormGroupCardWrapper>
  )
}

const renderModifierMultiSelect = (category: MerchandiseModifierCategory, toModifiers: ModifierMap) => {
  const { formState, control, getValues, trigger, watch } = useFormContext();
  const errors = formState.errors
  const formValues = getValues()

  const modifiers = useMemo(() => category.modifierIds.map((modifierId) => toModifiers[modifierId]), [category]);
  const categoryModifiersNames: string[] = pluck('name', modifiers)
  const categoryModifiersValues: boolean[] = watch(categoryModifiersNames)
  const errorMessage = pipe(pick(categoryModifiersNames), values, pathOr(undefined, [0, 'type']))(errors)
  const helperText = errorMessage ?? category.description as string | undefined

  //trigger validation on all category modifiers when any value changes
  // useEffect(() => {
  //   trigger(categoryModifiersNames)
  // }, [JSON.stringify(categoryModifiersValues)]);

  const hasNoMax = category.maxQuantity <= 0 || category.maxQuantity >= modifiers.length;
  const hasNoMin = category.minQuantity < 1;

  const isMaximumSelectionLimitReached = useMemo(() => {
    if (hasNoMax) return false;
    return checkSelectionLimitForCategory(category.maxQuantity, categoryModifiersValues, false)
  }, [category, categoryModifiersValues]);

  const verifyMinimumModifiersSelection = useCallback(() => {
    if (hasNoMin) return true;
    return checkSelectionLimitForCategory(category.minQuantity, categoryModifiersValues, true)
  }, [category, categoryModifiersValues]);

  const maxSelectionLimitText = useMemo(() => {
    const minimumText = category.minQuantity <= 0 ? '' : ` at least ${category.minQuantity}`;
    const maximumText = hasNoMax ? '' : ` up to ${category.maxQuantity}`;
    return (minimumText || maximumText) ? `(Select${minimumText}${maximumText} items)${category.minQuantity > 0 ? '*' : ''}` : '';
  }, [category]);

  return (
    <FormGroupCardWrapper key={category.id} id={category.id} elevation={2}>
      <FormControl
        fullWidth
        error={!isEmptyOrNil(errorMessage)}
        variant="standard">
        <FormLabel>{category.name} {maxSelectionLimitText}</FormLabel>
        <Divider />
        <FormGroup>
          {modifiers.map((m: MerchandiseModifier, index: number) => {
            const isDisabledAfterMaxSelection = isMaximumSelectionLimitReached && !propOr(false, m.name, formValues)
            const isCheckboxDisabled = !m?.available || isDisabledAfterMaxSelection

            return (m &&
              <div key={m.id}>
                {(index !== 0) && <Divider />}
                <Controller
                  defaultValue={m.isDefault && m.available}
                  name={`${m.name}` as const}
                  control={control}
                  rules={{
                    validate: {
                      required: verifyMinimumModifiersSelection
                    }
                  }}
                  render={({ field }) =>
                    <FormControlLabel
                      control={<Checkbox color="primary" defaultChecked={m.isDefault && m.available} {...field} disabled={isCheckboxDisabled} />}
                      label={getModifierLabel(m)}
                    />
                  }
                />
              </div>
            )
          })}
        </FormGroup>
        {helperText && <FormHelperText>{helperText}</FormHelperText>}
      </FormControl>
    </FormGroupCardWrapper>
  )
}

const ModifierTemplate = (props: {
  templateId: number | undefined,
  mercData: MerchandiseApiResponseType | undefined,
}) => {
  const { templateId, mercData } = props
  if (!templateId || !mercData) {
    return null
  }
  const template = mercData.modiTemplates.find((t) => (t.id === templateId))
  if (!template) {
    return null
  }

  const toModifiers: ModifierMap = {}
  mercData.modifiers.map((modifier) => toModifiers[modifier.id] = modifier)

  return (
    <div>
      {template.modifierCategoryIds.map((categoryId: number) => {
        const numCategoryId = Number(categoryId)
        const category = mercData.modiCategories.find((c) => (c.id === numCategoryId))

        if (!category) {
          return null
        }

        if (category.maxQuantity === 1 && category.minQuantity === 1) {
          return renderModifierRadioSelect(category, toModifiers)
        } else {
          return renderModifierMultiSelect(category, toModifiers)
        }

      })}
    </div>
  )
}

export default ModifierTemplate

