import React from 'react';
import { Grid, makeStyles, createStyles } from '@material-ui/core';
import LazyLoad from 'react-lazyload';

import { Merchant } from '../../services/models';
import MerchantCard from '../MerchantCard';

const useStyles = makeStyles(() =>
  createStyles({
    root: {
      '& .lazyload-wrapper': {
        height: '100%'
      }
    }
  })
)
export interface StoreListProps {
  merchants: Merchant[];
}

export const StoresListDetailed = (props: Readonly<StoreListProps>): JSX.Element => {
  const { merchants } = props;
  const classes = useStyles()

  return (
    <div className={classes.root}>
      <Grid container spacing={2}>
        {
          merchants?.map((merchant) => {
            return (
              <Grid item xs={12} sm={6} md={4} lg={3} key={merchant.id}>
                <LazyLoad offset={100} height={100} unmountIfInvisible>
                  <MerchantCard merchant={merchant} />
                </LazyLoad>
              </Grid>
            )
          })
        }
      </Grid>
    </div>
  );
}

export default StoresListDetailed;
