import React, { SyntheticEvent } from 'react';
import { makeStyles } from '@material-ui/styles';
import { Box, createStyles, Fab } from '@material-ui/core';
import { useHistory } from 'react-router';
import { Filter } from '@material-ui/icons';

import { FullscreenPaper } from '../bos_common/src';
import { Merchant } from '../bos_common/src/types/MerchantType';
import { StructuredAddress } from '../bos_common/src/types/AddressType';
import { EmbededMap } from '../bos_common/src/components/EmbededMap';
import { FabContainer } from '../bos_common/src/components/FabContainers';

import PageHeader from '../components/common/PageHeader';
import BackButton from '../components/common/BackButton';
import { useAppDispatch, useAppSelector } from '../redux/hooks';
import { searchMerchantByName, searchQueryUpdated } from '../redux/slice/merchant/action';
import { getFilteredMerchants, getMerchantLoadingState } from '../redux/slice/merchant/merchantSelector';
import SimpleLoader from '../bos_common/src/components/SimpleLoader';
import StoreSearchDrawer from '../components/Stores/StoreSearchDrawer';

const useStyles = makeStyles(() => createStyles({
  root: {
    '& .fab': {
      zIndex: 9999,
    },
  },
}));

interface EllipsFABProps {
  onClick?: (e: SyntheticEvent) => void
}

const EllipsFAB = (props: Readonly<EllipsFABProps>): JSX.Element => {
  const { onClick } = props;

  return (
    <FabContainer alignment='right'>
      <Fab variant="extended"
        sx={{ backgroundColor: 'rgba(0,0,0,0.1)', color: '#FFF' }}
        onClick={onClick}>
        <Filter />&nbsp;Filters
      </Fab>
    </FabContainer>
  );
}

const MapSearchPageHeader = (): JSX.Element => {
  const history = useHistory();

  const handleBack = () => {
    history.goBack();
  }

  return (
    <PageHeader
      title={"Map Search"}
      leftChild={<BackButton onBack={handleBack} />}
    />
  );
}

export const MapSearchPage = (): JSX.Element => {
  const [storeLocations, setStoreLocations] = React.useState<StructuredAddress[]>();
  const dispatch = useAppDispatch();
  const stores: Merchant[] = useAppSelector(getFilteredMerchants);
  const isFetchingMerchants: boolean = useAppSelector(getMerchantLoadingState);
  const classes = useStyles();

  React.useEffect(() => {
    dispatch(searchMerchantByName(''));
  }, [])

  React.useEffect(() => {
    if (isFetchingMerchants) return;
    const storeLocations: StructuredAddress[] = stores
      .filter((item) => item.structuredAddress && item.structuredAddress.lat && item.structuredAddress.lng && item.structuredAddress.country === 'US')
      .map<StructuredAddress>((item) => ({ ...item.structuredAddress, title: item.officialName, subtitle: item.description, id: item.username } || {}));

    setStoreLocations([...storeLocations]);
  }, [stores, isFetchingMerchants]);

  const handleChangeQuery = (query: string): void => {
    dispatch(searchQueryUpdated(query));
    dispatch(searchMerchantByName(query));
  }

  return (
    <Box>
      <FullscreenPaper className={classes.root}>
        <MapSearchPageHeader />
        <SimpleLoader loading={isFetchingMerchants} />
        <EmbededMap storeLocations={storeLocations || []} height={400} />
        <StoreSearchDrawer stores={stores} onChangeQuery={handleChangeQuery} />
        {/* <EllipsFAB /> */}
      </FullscreenPaper>
    </Box>
  );
}

export default MapSearchPage;
