import React from 'react';
import { useTheme } from '@material-ui/core';
import DiamondIcon from "../../assets/icons/DiamondIcon";
import { Merchant } from "../../services/models";
import { isEmptyOrNil } from "../../utils";


const renderMerchantRewards = (merchant: Merchant) => {
  const theme = useTheme();
  const isPointsAvailable =
    !isEmptyOrNil(merchant.orderingConfig?.pointsPerDollar) &&
    merchant.orderingConfig?.pointsPerDollar &&
    merchant.orderingConfig?.pointsPerDollar > 0;

  return isPointsAvailable
    ? (
      <span style={{
        color: theme.palette.secondary.main,
        fontWeight: 700,
        display: "flex",
        alignItems: "center",
        gap: theme.spacing(0.5),
      }}>
        <DiamondIcon color="secondary" width="10px" height="10px" />
        {merchant.orderingConfig?.pointsPerDollar}x Points
      </span>
    )
    : null;
}

export default renderMerchantRewards;