import React, { useContext, useEffect, useState } from 'react';
import {
  CardContent,
  Grid,
  IconButton,
  List,
  Typography,
} from '@material-ui/core';
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import { useHistory } from 'react-router-dom';

import { UserContext } from '../bos_common/src/context/UserContext';
import { Favorite, Merchant, Merchandise } from '../services/models'
import axios from '../bos_common/src/services/backendAxios';
import PageHeader from '../components/common/PageHeader';
import FavoriteButton from '../components/common/FavoriteButton';
import { ColoredPaper } from '../bos_common/src/components/Papers';
import { headerCustomStyle } from '../styles/Common.style';
import { Star } from '@material-ui/icons';
import SimpleLoader from '../bos_common/src/components/SimpleLoader';
import MerchantCard from '../components/MerchantCard';
import { If } from 'react-if';
import { isEmptyOrNil } from '../utils';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      borderRadius: "20px 20px 0 0 !important", // to override inline style
      zIndex: 1,
    },

    navWrapper: {
      ...headerCustomStyle(theme, theme.palette.primary.main),
    },
  })
);

interface FavData {
  favorites: Favorite[];
  merchants: Merchant[];
  merchandises: Merchandise[];
}

const FavoritesPage = (): React.ReactElement => {
  const title = 'Favorites'
  const history = useHistory()
  const classes = useStyles()
  const { user, loading } = useContext(UserContext);
  const [favLoading, setFavLoading] = useState<boolean>(false)
  const [data, setData] = useState<FavData>({
    favorites: new Array<Favorite>(),
    merchants: new Array<Merchant>(),
    merchandises: new Array<Merchandise>(),
  });

  useEffect(() => {
    if (user) {
      setFavLoading(true)
      axios.get<FavData>('/favorites/data', {
        params: {
          userId: user.id
        }
      }).then((response) => {
        setFavLoading(false)
        if (response.status === 200) {
          setData({
            favorites: response.data?.favorites,
            merchants: response.data?.merchants,
            merchandises: response.data?.merchandises,
          })
        }
      }).catch((err) => {
        console.log(err)
        setFavLoading(false)
      })
    }
  }, [user])


  const removeRecord = (merchant: Merchant, merchandise?: Merchandise) => {
    if (merchandise) {
      const newMerchandises = [...data.merchandises]
      const index = newMerchandises.indexOf(merchandise, 0);
      if (index > -1) {
        newMerchandises.splice(index, 1);
      }
      setData({
        favorites: data.favorites,
        merchants: data.merchants,
        merchandises: newMerchandises,
      })
    } else {
      const newMerchants = [...data.merchants]
      const index = newMerchants.indexOf(merchant, 0);
      if (index > -1) {
        newMerchants.splice(index, 1);
      }
      setData({
        favorites: data.favorites,
        merchants: newMerchants,
        merchandises: data.merchandises,
      })
    }
  }

  return (
    <div className="container">
      <SimpleLoader loading={loading || favLoading} />
      <div className={classes.navWrapper}>
        <PageHeader title={title} leftChild={<IconButton><Star fontSize='large' /></IconButton>} />
      </div>
      <ColoredPaper className={classes.root}>
        {user && (
          <CardContent>
            {!favLoading && data.merchants && data.merchants.length === 0 && (
              <Typography gutterBottom variant="subtitle1" component="div">
                No favorites yet
              </Typography>
            )}
            <Grid container spacing={2}>
              {!favLoading && data.merchants && data.merchants.map((merchant: Merchant) => {
                return (
                  <Grid item xs={12} sm={6} md={4} key={merchant.id}>
                    <MerchantCard merchant={merchant} />
                  </Grid>
                )
              })}
            </Grid>
          </CardContent>
        )}
      </ColoredPaper>
    </div>
  );
};

export default FavoritesPage;
