import React from 'react';
import LazyLoad from 'react-lazyload';
import { Merchant } from '../../services/models';
import MerchantItemCard from '../common/MerchantItemCard';

export interface StoreListProps {
  merchants: Merchant[];
}

export const StoresListCompact = (props: Readonly<StoreListProps>): JSX.Element => {
  const { merchants } = props;

  const renderStores = (): JSX.Element[] => {
    return merchants.map((item: Merchant) => (
      <LazyLoad key={item.id} offset={0} overflow unmountIfInvisible>
        <MerchantItemCard merchant={item} gutterBottom />
      </LazyLoad>
    ));
  }

  return (
    <div>
      {renderStores()}
    </div>
  );
}

export default StoresListCompact;
