const MARKETING_MESSAGES = {
  "supportlocal": 'Support Local with Every Order,\n$0 Commission',
  "directorderonline": 'Direct Order without the Hassle of Dialing',
  "localonline": 'All the Local Finds at the Same Time',
  "localdeals": 'Local Deals that San Mateo Locals Love',
  "buylocalgetrewards": 'Shop Small with Big Rewards in San Mateo',
  "1clicklocalorders": 'Keep $Local,\nKeep Direct Ordering Easy',
  "orderonlinenofees": 'Online Ordering with Offline Prices\n$0 Commission fees'
}

const getMarketingMessageToDisplay = (search: string) => {
  const params = qs.parse(search, { ignoreQueryPrefix: true })
  const cid = propOr('', 'cid', params)

  if (isEmptyOrNil(cid)) return MARKETING_MESSAGES.supportlocal
  return propOr(MARKETING_MESSAGES.supportlocal, cid, MARKETING_MESSAGES)
};

const SanMateoDiscoveryPage = () => {
  const classes = useStyles()
  const isMobile = useMediaQuery((theme: Theme) => theme.breakpoints.down('sm'));

  const [{ data, loading }] = useAxios<ClassifiedsApiResponseType>(
    { url: `/classifieds/data`, method: "GET" },
  )

  const classifiedsList = Object.entries(data || {})

  return (
    <div className="container">
      <SimpleLoader loading={loading} />
      <FullscreenPaper className={classes.root} square={true}>
        <Stack
          direction={"row"}
          alignItems={"center"}
          justifyContent={"space-between"}
          className="header"
        >
          <CardHeader
            component={Link}
            to={'/'}
            title={
              <Typography variant="h5" component="div" color={"primary"}>
                OneMarket
              </Typography>
            }
            avatar={<Avatar sx={{ width: 24, height: 24 }} color="secondary" alt={"One Market"} src={AppLogo} variant="square" />}
          />
          <Box className="location-box">
            <LocationOn fontSize='small' />&nbsp;<Typography >San Mateo, CA</Typography>
          </Box>
        </Stack>

        <Button
          variant="outlined"
          color="inherit"
          fullWidth
          component={Link}
          to={'/sanmateo/discovery'}
          size="large"
          className="exploreBtn"
        >
          Search in San Mateo...
        </Button>

        <div className="banner">
          <If condition={isMobile}>
            <Then>
              <img src={BannerOverlapImg} className="bannerOverlapImage" alt="" />
            </Then>
            <Else>
              <img className='bannerImg' src={BannerImg} alt="banner-img" />
            </Else>
          </If>
          <Typography className="illustrationTitle">{getMarketingMessageToDisplay(location.search)}</Typography>
        </div>

        <div className='classified-sections-container'>
          <If condition={!isEmptyOrNil(classifiedsList)}>
            <Then>
              {classifiedsList.map(([key, list]) => <ClassifiedCarousel key={key} list={list} />)}
            </Then>
          </If>
        </div>
        <FabContainer
          alignment='right'
          fabStyle={{ padding: "24px 16px 16px" }}
          fadeTop={isMobile}
        >
          <MenuDrawer menuButtonProps={{ color: "primary", variant: "contained" }} />
        </FabContainer>
      </FullscreenPaper>
    </div>
  );
}

export default SanMateoDiscoveryPage;
