import React, { useEffect, useState } from 'react'
import { createStyles, LinearProgress, makeStyles, Theme, Typography } from '@material-ui/core';

interface Props {
  onCompleteAction: () => void,
  autoRedirectDuration?: number
}

const useStyles = makeStyles((theme: Theme) => createStyles({
  root: {
    marginBottom: theme.spacing(2)
  },
}))

const AutoRedirectProgress = ({ onCompleteAction, autoRedirectDuration = 60 }: Props): React.ReactElement => {
  const [progress, setProgress] = useState(0);

  const classes = useStyles()

  useEffect(() => {
    const timer = setInterval(() => {
      setProgress((prevProgress) => (prevProgress >= autoRedirectDuration ? autoRedirectDuration : prevProgress + 1));
    }, 1000);
    return () => {
      clearInterval(timer);
    };
  }, []);

  if (progress >= autoRedirectDuration && onCompleteAction) { onCompleteAction(); }

  return <div className={classes.root}>
    <Typography variant="subtitle1" color="textSecondary" component="p">Auto redirect in {autoRedirectDuration - progress}s</Typography>
    <LinearProgress variant="determinate" value={progress * (100 / autoRedirectDuration)} />
  </div>
}

export default AutoRedirectProgress
