const MARKETING_MESSAGES = {
  "supportlocal": 'Support Local with Every Order,\n$0 Commission',
  "directorderonline": 'Direct Order without the Hassle of Dialing',
  "localonline": 'All the Local Finds at the Same Time',
  "localdeals": 'Local Deals that San Mateo Locals Love',
  "buylocalgetrewards": 'Shop Small with Big Rewards in San Mateo',
  "1clicklocalorders": 'Keep $Local,\nKeep Direct Ordering Easy',
  "orderonlinenofees": 'Online Ordering with Offline Prices\n$0 Commission fees'
}

const getMarketingMessageToDisplay = (search: string) => {
  const params = qs.parse(search, { ignoreQueryPrefix: true })
  const cid = propOr('', 'cid', params)

  if (isEmptyOrNil(cid)) return MARKETING_MESSAGES.supportlocal
  return propOr(MARKETING_MESSAGES.supportlocal, cid, MARKETING_MESSAGES)
};

interface SearchStateType {
  sortBy: string,
  price: number | undefined,
  name: string,
  selectedCategory: string
}

const MerchantDiscoveryPage = (): React.ReactElement => {
  const classes = useStyles();
  const { featureFlags } = useContext(AppContext);
  const location = useLocation();

  const [searchState, setSearchState] = useWindowStorage<SearchStateType>(
    LOCALSTORAGE_MARKETPLACE_APP_KEYS.MARKETPLACE_DISCOVERY_SEARCH_STATE,
    {
      sortBy: '',
      price: undefined,
      name: '',
      selectedCategory: ''
    },
    'sessionStorage'
  )

  const [storeLocations, setStoreLocations] = useState<StructuredAddress[]>();
  const [showOnlineOnlyMerchants, setShowOnlineOnlyMerchants] = useState<boolean>(false)

  const isMobile = useMediaQuery((theme: Theme) => theme.breakpoints.down('sm'));

  const [{ data: merchantsList, loading: isFetchingMerchants }, executeCall] = useAxios<Merchant[]>(
    { url: `/merchants/search`, method: "GET" },
    { manual: true }
  )

  // execute Search Call where search state is updated
  useEffect(() => {
    executeCall({
      params: {
        limit: 100, // provided limit 1000 because a param with null value is not sent in the request
        claimed: false,
        ...(!isEmptyOrNil(searchState.name) && { name: searchState.name }),
        ...(!isEmptyOrNil(searchState.selectedCategory) && { category: searchState.selectedCategory }),
        ...(!isEmptyOrNil(searchState.price) && { expenseLevel: searchState.price }),
      }
    })
  }, [searchState]);

  const filteredMerchantsList = useMemo(() => merchantsList?.filter(i => {
    return showOnlineOnlyMerchants ? isMerchantOnline(i) : true
  }) || [], [merchantsList, showOnlineOnlyMerchants]);

  useEffect(() => {
    if (!filteredMerchantsList) return;
    const storeLocations: StructuredAddress[] = filteredMerchantsList
      .filter((item) => item.structuredAddress && item.structuredAddress.lat && item.structuredAddress.lng && item.structuredAddress.country === 'US')
      .map<StructuredAddress>((item) => ({
        ...item.structuredAddress,
        id: item.username,
        title: item.officialName,
        subtitle: item.description
      } || {}));

    setStoreLocations([...storeLocations]);
  }, [filteredMerchantsList]);

  const scrollToTop = () => {
    const el = document.getElementById('marketplace-page') as HTMLDivElement
    if (!isEmptyOrNil(el)) {
      el.scrollTo({
        top: 0,
        behavior: 'smooth'
      })
    }
  }

  const handleChange = (key: string, value: any) => {
    setSearchState((s) => ({
      ...s,
      [key]: value
    }))
  }

  const onChangeQuery = (query: string): void => {
    handleChange('name', query)
    scrollToTop()
  }

  // if (featureFlags && !enableSanMateoMP(featureFlags)) {
  //   return (
  //     <Redirect
  //       to={{
  //         pathname: '/',
  //         state: { from: location },
  //       }}
  //     />
  //   )
  // }

  return (
    <div className={classes.root} id="marketplace-page">
      <div className="banner">
        <div className="search-box">
          <StoreSearchBox
            onChangeQuery={onChangeQuery}
            placeholder='Search in San Mateo...'
            defaultValue={searchState.name}
            leftChild={
              <IconButton sx={{ p: 0 }} component={Link} to={'/sanmateo'}>
                <NavigateBefore fontSize="large" color="primary" />
              </IconButton>
            }
          />
        </div>
        <EmbededMap storeLocations={storeLocations || []} height={350} />
      </div>
      <ColoredPaper variant="outlined" square={false} className="content">
        <SimpleLoader loading={isFetchingMerchants} />
        <CardContent>
          <Stack
            direction="row"
            alignItems="center"
            spacing={2}
            overflow="auto"
            sx={{ marginBottom: 1 }}
          >
            <StyledChip
              label="Open Now"
              variant={"filled"}
              icon={<OpenNowIcon />}
              className="filters-chip"
              color={showOnlineOnlyMerchants ? "primary" : 'default'}
              onClick={() => setShowOnlineOnlyMerchants(!showOnlineOnlyMerchants)}
            />
            <Divider orientation="vertical" flexItem />

            {/* <Box sx={{ minWidth: 120 }}>
              <FormControl fullWidth variant="outlined" size='small'>
                <InputLabel id='expense-level'>Price</InputLabel>
                <Select
                  labelId='expense-level'
                  label="Price"
                  value={searchState.price}
                  onChange={(e) => handleChange('price', e.target.value)}
                >
                  <MenuItem value={''}>None</MenuItem>
                  {MERCHANT_EXPENSE_LEVELS.map(option => <MenuItem key={option.value} value={option.value}>{option.label}</MenuItem>)}
                </Select>
              </FormControl>
            </Box> */}

            {
              MERCHANTS_CATEGORIES_LIST.map((category) => {
                const isCategorySelected = searchState.selectedCategory === category.value
                return <StyledChip
                  key={category.value}
                  label={category.label}
                  icon={isCategorySelected && category.selectedIcon ? category.selectedIcon : category.icon}
                  variant={"filled"}
                  className="filters-chip"
                  color={isCategorySelected ? "primary" : 'default'}
                  onClick={() => handleChange('selectedCategory', isCategorySelected ? '' : category.value)}
                />
              })
            }
          </Stack>
        </CardContent>

        <CardContent>
          <Typography variant='subtitle1' component={'div'} gutterBottom>
            Filtered
            <Typography variant='subtitle1' component={'span'} color="primary">
              &nbsp;{filteredMerchantsList.length < 10 && filteredMerchantsList.length > 0 ? '0' : ''}{filteredMerchantsList.length}&nbsp;
            </Typography>
            local <Typography variant='subtitle1' component={'span'} color="primary">{searchState.selectedCategory}</Typography> merchants
          </Typography>

          <StoresListDetailed merchants={filteredMerchantsList} />
        </CardContent>


        <FabContainer
          alignment='right'
          fabStyle={{ padding: "24px 16px 16px" }}
          fadeTop={isMobile}
        >
          <MenuDrawer menuButtonProps={{ color: "primary", variant: "contained" }} />
        </FabContainer>
      </ColoredPaper>
    </div>
  )
}

export default MerchantDiscoveryPage;