import React from "react";
import { TypographyProps, Typography, makeStyles, createStyles } from "@material-ui/core";
import ReactMarkdown from "react-markdown";
import { Merchandise } from "../../types/MerchandiseType";

const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
      position: 'relative',
      '& .block-with-text': {
        display: '-webkit-box',
        WebkitBoxOrient: 'vertical',
        WebkitLineClamp: 3,
        overflow: 'hidden',
        textOverflow: 'ellipsis',
        maxHeight: '50px',  // for 3 lines based on 1.2 lineheight 1.2*3*14px = 50px
        '& p': {
          margin: 0,
          lineHeight: 1.2,
          fontWeight: 400,
        },
      },
    },
  })
);


type MerchandiseDescriptionProp = {
  merchandise: Merchandise,
  typographProps?: TypographyProps<"div">,
}

const MerchandiseDescription = ({ merchandise, typographProps }: MerchandiseDescriptionProp): React.ReactElement  => {
  const styles = useStyles();

  return (
    <Typography variant="subtitle2" color="textSecondary" component="div" {...typographProps} className={styles.root}>
      <ReactMarkdown className={'block-with-text'}>
        {merchandise.description}
      </ReactMarkdown>
    </Typography>
  )
}

export default MerchandiseDescription;