import React, { useContext, useEffect, useState } from "react";
import { useHistory } from "react-router";
import {
  Alert, CardContent, Divider, Theme, Fab,
  useMediaQuery, SwipeableDrawer, Toolbar, Typography, Button
} from "@material-ui/core";
import { CreditCard } from "@material-ui/icons";
import { If, Then, Else, Switch, Case, Default } from 'react-if';

import { getCartDiscountedAmount, getTotalItems, getTotalPrice } from "../services/cartUtils";
import { AppContext, CartItemType } from "../context/AppContext";
import { ColoredPaper } from '../bos_common/src/components/Papers';
import { MultiFabContainer } from "../bos_common/src/components/FabContainers";
import { UserContext } from "../bos_common/src/context/UserContext";
import renderPrice from "../bos_common/src/components/Price";

import CartItems from "../components/ShoppingCart/CartItems";
import BackButton from "../components/common/BackButton";
import TipOptions from "../components/ShoppingCart/TipOptions";
import ShoppingCartHeader from "../components/ShoppingCart/ShoppingCartHeader";
import CartSummary from "../components/ShoppingCart/CartSummary";
import ShoppingCartCouponInput from "../components/ShoppingCart/ShoppingCartCouponInput";
import PointsRedemptionButton from "../components/ShoppingCart/PointsRedemptionButton";
import { enableRedeemPoints, isEmptyOrNil, isKioskDevice, isStoreOpen, toFixed2, getRequiredRedemptionPoints, isOpenCheckEnabled, isAddingItemToOpenTab, isDineInPaymentDisabled } from "../utils";
import { ShoppingCartContext } from "../context/ShoppingCart/ShoppingCartContext";
import { isCouponEnabled } from "../bos_common/src/services/featureFlagUtils";
import { OrderType } from "../services/models";
import SignupDrawer, { SignupEntrance } from "../components/User/SignupDrawer";
import { CartBagItemCart } from "../components/ShoppingCart/CartBagItemCard";
import { OrderPickupTypeOptions } from "../components/ShoppingCart/OrderPickupTypeOptions";
import { captureClickEvent, EVENT_TYPES } from "../services/EventsTracking";

const zeroCurrencyRegExpt = new RegExp(/(\$)0.0+/, 'gi');

const RenderPickUpCartContent = ({ isValidPickupTime }: { isValidPickupTime: boolean }) => {
  const history = useHistory();
  const { featureFlags, merchantConfig, cart, tip, coupon } = useContext(AppContext)
  const { soldoutItems, clearSoldOutItems } = useContext(ShoppingCartContext);
  const isMobile = useMediaQuery((theme: Theme) => theme.breakpoints.down('sm'));
  const { orderingConfig } = merchantConfig?.merchant || {};

  const count = getTotalItems(cart);
  const total = getTotalPrice({ cart, tip, coupon, orderingConfig });

  const RenderFabCTA = () => {
    const areCartItemsOutOfOrder = !isEmptyOrNil(soldoutItems);
    const price = renderPrice(total);

    return (
      <MultiFabContainer fabStyle={{ padding: "24px 0 16px" }} fadeTop={isMobile}>
        <Switch>
          <Case condition={areCartItemsOutOfOrder}>{ () =>
            <Fab color="primary" variant="extended" size="large" onClick={clearSoldOutItems}>
              Clear unavailable items
            </Fab>}
          </Case>
          <Case condition={count < 1}>{ () =>
            <Fab color="primary" variant="extended" size="large" disabled>
              No items to checkout
            </Fab>}
          </Case>
          <Case condition={!isValidPickupTime}>{ () =>
            <Fab color="primary" variant="extended" size="large" disabled>
              Select a pick up time
            </Fab>}
          </Case>
          <Default>{ () =>
            <>
              <PointsRedemptionButton />
              <Fab
                color="primary"
                variant="extended"
                size="large"
                onClick={() => history.push("/checkout")}
              >
                <CreditCard fontSize="large" />&nbsp;
                {zeroCurrencyRegExpt.test(price) ? `Checkout ${price}` : `Continue to Payment ${price}`}
              </Fab>
            </>}
          </Default>
        </Switch>
      </MultiFabContainer>
    )
  }

  return (
    <CardContent className={"content"}>
      <If condition={count === 0}>
        <Then>
          <Alert severity="error">
            {"You don't have any item in your cart"}
          </Alert>
        </Then>
        <Else>
          <CartItems />
          <CartBagItemCart />
          <OrderPickupTypeOptions />
          <TipOptions />
          <If condition={isCouponEnabled(featureFlags)}>
            <ShoppingCartCouponInput />
          </If>
          <Divider />
          <CartSummary />
        </Else>
      </If>
      <RenderFabCTA />
    </CardContent>
  )
}

const RenderDineInCartContent = (props: {
  isValidPickupTime: boolean,
  isCheckoutPage: boolean
}) => {
  const { isValidPickupTime = false, isCheckoutPage = false } = props
  const history = useHistory()
  const { cart, merchantConfig, tip, coupon, featureFlags, cartType } = useContext(AppContext)
  const { soldoutItems, clearSoldOutItems } = useContext(ShoppingCartContext)
  const [showSignup, setShowSignup] = useState<boolean>(false)
  const [showPayOptionsDrawer, togglePayOptionsDrawer] = useState<boolean>(false)
  const { user } = useContext(UserContext)

  const count = getTotalItems(cart)
  const total = getTotalPrice({ cart, tip, coupon, orderingConfig: merchantConfig?.merchant.orderingConfig })
  const isDisabled = !isValidPickupTime || count < 1
  const isOpenCheckCart = !isCheckoutPage && isOpenCheckEnabled(merchantConfig?.merchant)

  const isPaymentDisabledForDineIn = isDineInPaymentDisabled(merchantConfig?.merchant);

  const handleOpenCheckOrderCheckout = () => {
    togglePayOptionsDrawer(false)
    if (isEmptyOrNil(user)) {
      setShowSignup(true)
    } else {
      history.push("/place-open-check-order")
    }
  }

  const handleOrderCheckout = () => {
    captureClickEvent({
      category: EVENT_TYPES.ORDER_CHECKOUT_STARTED,
      data: {
        merchant: merchantConfig?.merchant,
        user
      }
    })

    history.push("/checkout")
  }

  const RenderFabCTA = () => {
    const areCartItemsOutOfOrder = !isEmptyOrNil(soldoutItems);

    return (
      <MultiFabContainer>
        <Switch>
          <Case condition={areCartItemsOutOfOrder}>{ () =>
            <Fab color="primary" variant="extended" size="large" onClick={clearSoldOutItems}>
              Clear unavailable items
            </Fab>}
          </Case>
          <Case condition={count < 1}>{ () =>
            <Fab color="primary" variant="extended" size="large" disabled>
              No items to checkout
            </Fab>}
          </Case>
          <Case condition={isOpenCheckCart && isAddingItemToOpenTab(cartType)}>{() =>
            <Fab
              color="primary"
              variant="extended"
              size="large"
              onClick={() => history.push("/place-open-check-order")}>
              Add to Open Tab
            </Fab>}
          </Case>
          <Case condition={isOpenCheckCart && !isAddingItemToOpenTab(cartType)}>{() =>
            <Fab
              color="primary"
              variant="extended"
              size="large"
              onClick={() => isPaymentDisabledForDineIn ? handleOpenCheckOrderCheckout() : togglePayOptionsDrawer(true)}>
              {
                isPaymentDisabledForDineIn ? "Open a Tab and Pay Later" : `Continue to payment ${renderPrice(total)}`
              }
            </Fab>}
          </Case>
          <Case condition={!isOpenCheckCart && isPaymentDisabledForDineIn}>{() =>
            <Alert severity="warning">
              {"Online payment is not supported at this store. Please checkout at the counter or ask for paper check."}
            </Alert>}
          </Case>
          <Case condition={!isValidPickupTime}>{ () =>
            <Fab color="primary" variant="extended" size="large" disabled>
              Select a pick up time
            </Fab>}
          </Case>
          <Default>{ () =>
            <>
              <PointsRedemptionButton />
              <Fab
                color="primary"
                variant="extended"
                size="large"
                disabled={isDisabled}
                onClick={handleOrderCheckout}
              >
                {`Continue to payment ${renderPrice(total)}`}
              </Fab>
            </>}
          </Default>
        </Switch>
      </MultiFabContainer>
    )
  }

  const PayOptionsDrawer = () => {


    const handlePayNowClick = () => {
      togglePayOptionsDrawer(false)
      history.push("/dine-in-cart")
    }

    return (
      <SwipeableDrawer
        variant="temporary"
        ModalProps={{
          keepMounted: false,
        }}
        anchor={'bottom'}
        open={showPayOptionsDrawer}
        onOpen={() => togglePayOptionsDrawer(true)}
        onClose={() => togglePayOptionsDrawer(false)}
      >
        <ColoredPaper
          role="presentation"
          className="toolbarWrapper"
        >
          <Toolbar className="toolbar" sx={{ justifyContent: 'center' }}>
            <Typography variant="h6" className="drawerTitle" component="div">
              How do you want to pay?
            </Typography>
          </Toolbar>
          <CardContent sx={{ display: 'flex', justifyContent: 'center', flexDirection: 'column', alignItems: 'center' }}>
            <Button
              color="primary"
              variant="outlined"
              size="large"
              disabled={isDisabled}
              onClick={handlePayNowClick}
              sx={{ maxWidth: 550, marginBottom: 1 }}
              fullWidth
            >
              Pay Now
            </Button>
            <Button
              color="primary"
              variant="contained"
              size="large"
              disabled={isDisabled}
              onClick={handleOpenCheckOrderCheckout}
              sx={{ maxWidth: 550 }}
              fullWidth
            >
              Open a Tab and Pay Later
            </Button>
          </CardContent>
        </ColoredPaper>
      </SwipeableDrawer>
    )
  }

  return (
    <CardContent className={"content"}>
      <If condition={count === 0}>
        <Then>
          <Alert severity="info">
            {"You don't have any item in your cart"}
          </Alert>
        </Then>
        <Else>
          <CartItems />
          <Divider />
          {!isOpenCheckCart &&
            <>
              <TipOptions />
              <If condition={isCouponEnabled(featureFlags) && !isPaymentDisabledForDineIn}>
                <ShoppingCartCouponInput />
              </If>
              <Divider />
            </>
          }
          <CartSummary />
        </Else>
      </If>
      <RenderFabCTA />
      <SignupDrawer
        open={showSignup}
        setOpen={setShowSignup}
        redirectAfterSignup="/place-open-check-order"
        entrance={SignupEntrance.PLACE_ORDER}
      />
      <PayOptionsDrawer />
    </CardContent>
  )
}

const ShoppingCartPage = ({ isCheckoutPage = false }: { isCheckoutPage?: boolean }): React.ReactElement => {
  const { cart, merchantConfig, featureFlags, pickupTime, orderType, cartType, openCheckOrder, coupon } = useContext(AppContext);
  const { pointsRedeemedInfo, setPointsRedemptionInfo } = useContext(ShoppingCartContext);
  const { user } = useContext(UserContext);

  const isRedeem1mPointsEnabled = enableRedeemPoints(featureFlags);
  const isKioskModeEnabled = isKioskDevice();

  const isValidPickupTime = isStoreOpen(merchantConfig?.merchant, pickupTime ? new Date(pickupTime) : new Date())
  const isDineInOrderType = orderType === OrderType.DINEIN

  const calculateAmountForRedeemedItems = () => {
    let hasChanged = false;
    let userWalletPoints = user?.wallet?.points || 0
    let redeemAmount = 0;
    let redeemPoints = 0;
    let couponAmount = getCartDiscountedAmount(cart, coupon);

    const redeemedCartItems = cart.map((item: CartItemType) => {
      const pointsPerItem = getRequiredRedemptionPoints(item, merchantConfig?.merchant)
      let quantityRedeemed = 0;

      for (let i = 0; i < item.quantity; i++) {
        let remainingPrice = Number(item.price);
        if (couponAmount > 0) {
          // minus coupon values first
          const toRedeemCouponAmount = Math.min(couponAmount, remainingPrice);
          couponAmount = couponAmount - toRedeemCouponAmount;
          remainingPrice = remainingPrice - toRedeemCouponAmount;
        }

        const remainingPointsToRedeem = remainingPrice / item.price * pointsPerItem;
        if (remainingPointsToRedeem > 0) {

          const toRedeemPoints = Math.min(userWalletPoints, remainingPointsToRedeem);
          userWalletPoints -= toRedeemPoints;
          redeemPoints += toRedeemPoints;
          redeemAmount += toRedeemPoints / pointsPerItem * item.price;
          quantityRedeemed += (toRedeemPoints / pointsPerItem);
        }
      }
      return {
        ...item,
        quantityRedeemed: quantityRedeemed,
      }
    });

    if (redeemPoints !== pointsRedeemedInfo.redeemPointsToBeApplied || redeemAmount !== pointsRedeemedInfo.redeemAmount) {
      hasChanged = true;
    }

    if (hasChanged) {
      // This will update the cart, which will call this method again, to avoid infinite loops, we need to check for actual change
      setPointsRedemptionInfo({
        redeemPointsToBeApplied: redeemPoints,
        redeemAmount: toFixed2(redeemAmount),
        items: redeemedCartItems,
      })
    }
  }

  useEffect(() => {
    if (!isEmptyOrNil(user) && !isKioskModeEnabled && isRedeem1mPointsEnabled) {
      calculateAmountForRedeemedItems()
    }
  }, [user, cart, isRedeem1mPointsEnabled, coupon])

  const title = isAddingItemToOpenTab(cartType)
    ? "Add Items to Order"
    : openCheckOrder ?
      `Check from ${merchantConfig?.merchant?.officialName ?? 'Store'}` :
      `Shopping Cart`

  return (
    <div className="container">
      <ShoppingCartHeader
        leftChild={<BackButton />}
        orderType={orderType}
        title={title}
        merchant={merchantConfig?.merchant} />
      <ColoredPaper>
        <If condition={isDineInOrderType}>
          <Then><RenderDineInCartContent isValidPickupTime={isValidPickupTime} isCheckoutPage={isCheckoutPage} /></Then>
          <Else><RenderPickUpCartContent isValidPickupTime={isValidPickupTime} /></Else>
        </If>
      </ColoredPaper>
    </div>
  )
}

export default ShoppingCartPage;