import React from "react";
import { createStyles, Drawer, makeStyles, SwipeableDrawer, Theme } from "@material-ui/core";

const useStyles = makeStyles((theme: Theme) => createStyles({
  root: {
    textAlign: "center",

    '& .MuiPaper-root': {
      padding: `${theme.spacing(4)} ${theme.spacing(2)}`,
      borderRadius: "20px 20px 0 0",
      overflowY: "unset",

      '&:before': {
        content: '""',
        position: "absolute",
        top: "-10px",
        left: "50%",
        transform: "translate(-50%)",
        width: "130px",
        height: "6px",
        borderRadius: "4px",
        background: "rgba(255, 255, 255, 0.5)",
        pointerEvents: "none"
      },

      '& .drawerImage': {
        maxWidth: "180px",
        width: "100%",
        marginBottom: theme.spacing(2),
        margin: "auto",
      },

      '& .drawerHeading': {
        margin: 0,
        marginBottom: theme.spacing(1),
        color: "#364245",
        fontSize: 20,
        fontWeight: 600,
      },

      '& .drawerDescription': {
        color: theme.palette.grey[600],
        fontSize: 14,
        lineHeight: 1.3
      }
    }
  }
}))

type CustomDrawerProps = {
  position?: "top" | "right" | "bottom" | "left";
  isOpen: boolean;
  setIsOpen: (value: boolean) => void;
  title: string;
  description: string;
  promotionImage?: string,
  children?: React.ReactElement;
};

const CustomDrawer = (props: CustomDrawerProps) => {
  const { position = "bottom", isOpen, setIsOpen, title, description, children, promotionImage = "/promotion-image.png" } = props;

  const classes = useStyles()

  const toggleDrawer = (open: boolean) => (
    event: React.KeyboardEvent | React.MouseEvent,
  ) => {
    if (
      event?.type === 'keydown' &&
      ((event as React.KeyboardEvent).key === 'Tab' ||
        (event as React.KeyboardEvent).key === 'Shift')
    ) {
      return;
    }

    setIsOpen(open);
  };

  return (
    <SwipeableDrawer
      className={classes.root}
      anchor={position}
      open={isOpen}
      onOpen={toggleDrawer(true)}
      onClose={toggleDrawer(false)}>
      <img className="drawerImage" src={promotionImage} />
      <h3 className="drawerHeading">{title}</h3>
      <p className="drawerDescription">{description}</p>
      {children && children}
    </SwipeableDrawer>
  );
};

export default CustomDrawer;
