import React, { useContext, useEffect, useState } from "react";
import { createStyles, IconButton, makeStyles, Theme } from "@material-ui/core";
import { FavoriteBorderSharp, Favorite as FavIcon } from "@material-ui/icons";
import useAxios from "axios-hooks";

import { UserContext } from "../../bos_common/src/context/UserContext";
import axios from "../../bos_common/src/services/backendAxios";
import { Merchandise, Merchant, Favorite } from "../../services/models";
import SignupDrawer, { SignupEntrance } from "../User/SignupDrawer";
import { isKioskDevice } from "../../utils";
import { useLocation } from "react-router";
import { rgba } from "polished";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      '&.MuiButtonBase-root': {
        backgroundColor: `${rgba(theme.palette.primary.main, 0.15)}`,
        transition: 'all 200ms linear',

        '&:hover': {
          backgroundColor: `${rgba(theme.palette.primary.main, 0.3)}`,
        }
      }
    },
  })
);

const FavoriteButton = (props: {
  merchant: Merchant;
  merchandise?: Merchandise;
  onUpdateSuccess?: (favorited: boolean) => void;
}): React.ReactElement | null => {
  if (isKioskDevice()) {
    return null;
  }

  const { user } = useContext(UserContext);
  const [favorited, setFavorited] = useState<boolean>(false);
  const [favLoading, setFavLoading] = useState<boolean>(false);
  const [showSignup, setShowSignup] = useState<boolean>(false);

  const location = useLocation();
  const isFavoriteCallback = location.hash === "#favorite";

  const [, executeFavoritePut] = useAxios(
    { url: "/favorite/update", method: "POST" },
    { manual: true }
  );

  const toggleMerchantFavoriteState = (newFav: boolean) => {
    setFavorited(newFav);

    setFavLoading(true);
    executeFavoritePut({
      data: {
        userId: user?.id,
        merchantId: props.merchant.id,
        merchandiseId: props.merchandise?.id,
        favorited: newFav,
      },
    })
      .then((response) => {
        setFavLoading(false);
        if (response.status === 200) {
          props.onUpdateSuccess && props.onUpdateSuccess(newFav);
        } else {
          setFavorited(!newFav);
        }
      })
      .catch((err) => {
        setFavLoading(false);
        setFavorited(!newFav);
      });
  };

  const getStoreFavoriteState = () => {
    setFavLoading(true);
    axios
      .get<Favorite[]>("/favorites", {
        params: {
          userId: user?.id,
          merchantId: props.merchant.id,
          merchandiseId: props.merchandise?.id,
        },
      })
      .then((response) => {
        setFavLoading(false);
        if (response.status === 200) {
          const isFavorite = response.data.length > 0;
          setFavorited(isFavorite);
          if (!isFavorite && isFavoriteCallback)
            toggleMerchantFavoriteState(true);
        }
      })
      .catch((err) => {
        console.log(err);
        setFavLoading(false);
      });
  };

  useEffect(() => {
    if (user) {
      getStoreFavoriteState();
    }
  }, [user]);

  const saveFavorite = () => {
    if (user && props.merchant) {
      toggleMerchantFavoriteState(!favorited);
    } else {
      setShowSignup(true);
    }
  };

  const redirectUrl = location.pathname + "#favorite";
  const classes = useStyles();

  return (
    <>
      <IconButton
        className={classes.root}
        aria-label="favorite"
        disabled={favLoading}
        onClick={saveFavorite}
      >
        {favorited ? (
          <FavIcon color="primary" />
        ) : (
          <FavoriteBorderSharp color="primary" />
        )}
      </IconButton>
      {showSignup && (
        <SignupDrawer
          open={showSignup}
          setOpen={setShowSignup}
          entrance={SignupEntrance.FAVORTIE}
          redirectAfterSignup={redirectUrl}
        />
      )}
    </>
  );
};

export default FavoriteButton;
