const MenuIconRenderer = (props: {
  value: string,
  path: string,
  label: string,
  icon: React.ReactNode
}) => {
  const { value, path, label, icon } = props

  return (
    <ListItem button key={value} component={NavLink} to={path} exact activeClassName="selected" >
      <ListItemIcon>{icon}</ListItemIcon>
      <ListItemText primary={label} />
      <ChevronRight />
    </ListItem>
  )
}

interface MenuDrawerProps {
  menuButtonProps?: {
    color?: "primary" | "secondary" | "inherit",
    variant?: 'text' | 'outlined' | 'contained'
  }
}

export const MenuDrawer = ({ menuButtonProps }: MenuDrawerProps): React.ReactElement => {
  const history = useHistory()
  const classes = useStyles()
  const location = useLocation()
  const { user, setAuthenticating, setLoginState } = useContext(UserContext)
  const { featureFlags } = useContext(AppContext);

  const openDrawer = location.hash?.includes('openDrawer')
  const [drawerOpen, setDrawerOpen] = useState(openDrawer || false);
  const [signupDrawerOpen, setSignupDrawerOpen] = useState(false);
  const isMarketPlaceEnabled = enableSanMateoMP(featureFlags);

  const toggleDrawer = (open: boolean) => (
    event: React.KeyboardEvent | React.MouseEvent,
  ) => {
    if (
      event.type === 'keydown' &&
      ((event as React.KeyboardEvent).key === 'Tab' ||
        (event as React.KeyboardEvent).key === 'Shift')
    ) {
      return;
    }

    setDrawerOpen(open);
    // remove the hash without refreshing the page
    if (!open && openDrawer) {
      window.location.hash = ''
    }
  }

  const userLogout = () => {
    setLoginState(undefined, undefined)
    sessionStorage.removeItem(LOCALSTORAGE_APP_KEYS.USER_PHONE_NUMBER);
    sessionStorage.removeItem(LOCALSTORAGE_APP_KEYS.USER_DISPLAY_NAME);

    if (isEmbeddedMode() && window?.webkit && window?.webkit.messageHandlers.logoutMessageHandler) {
      window?.webkit.messageHandlers.logoutMessageHandler.postMessage('');
    } else if (isEmbeddedMode() && window?.logoutMessageHandler) {
      window.logoutMessageHandler.postMessage('')
    } else {
      setAuthenticating(true)
      logout(window.location.origin + '/logout', location.pathname);
    }
  }

  const setMarketPlaceButton = (str: string, mpString: string) => {
    if (!isMarketPlaceEnabled) return str;
    return location.pathname !== '/'
      ? str
      : mpString
  }

  const displayMarketPlaceButton = () => {
    if (!isMarketPlaceEnabled && location.pathname === '/') return false
    return true
  }

  const MarketPlaceButton = (
    <Button
      variant="contained"
      color="primary"
      size="large"
      startIcon={<Language />}
      sx={{ marginTop: 2 }}
      onClick={() => history.push(setMarketPlaceButton('/', '/sanmateo'))}
      className="drawer-action-btn"
      fullWidth
    >
      {setMarketPlaceButton('Go to 1M Homepage', 'Go to San Mateo Marketplace')}
    </Button>
  )

  const OneMarketSupportButton = (
    <ListItem
      button
      key={'one-market-support-button'}
      component={'a'}
      href="https://docs.google.com/forms/d/1HA3tBbmJXt63FYo9NLfE5oCRpKgXiwgXlY0UV7SmeHA/edit"
      target="_blank"
    >
      <ListItemIcon><Support /></ListItemIcon>
      <ListItemText primary={'Contact Support'} />
    </ListItem>
  )

  const RenderAnonymousList = () => {
    return (
      <Box
        className={classes.drawer}
        role="presentation">
        <Toolbar className="toolbar" sx={{ justifyContent: 'center' }}>
          <div>
            <img src={process.env.PUBLIC_URL + '/delivery-guy-handing-box.png'} />
            <Typography variant="h4" component="div" color="primary" sx={{ marginTop: 2 }}>
              <Icon component={AppLogoIcon} />&nbsp;One Market
            </Typography>
          </div>
        </Toolbar>
        <Divider />
        <List className='content'>
          <MenuIconRenderer
            value="discover"
            path="/discovery"
            label="Discover"
            icon={<OneMarketIcon />}
          />
          <MenuIconRenderer
            value="visit-history"
            path="/visit-history"
            label="Visit History"
            icon={<AccessTimeFilled color="secondary" />}
          />
          {OneMarketSupportButton}
        </List>

        <Divider />
        {displayMarketPlaceButton() && MarketPlaceButton}
        <Button
          variant="contained"
          color="primary"
          size="large"
          startIcon={<Login />}
          fullWidth
          sx={{ marginBottom: 2, marginTop: 2 }}
          className="drawer-action-btn"
          onClick={() => setSignupDrawerOpen(true)}
        >
          Login to Enjoy More!
        </Button>
        <VersionNumber />
      </Box>
    )
  }

  const RenderUserList = (currentUser: User | undefined) => {
    if (!currentUser) {
      return null
    }

    return (
      <Box
        className={classes.drawer}
        role="presentation">
        <Toolbar className="toolbar">
          <Avatar src={user?.picture} alt="Profile-pic" sx={{ width: 56, height: 56, marginRight: 2 }} />
          <div>
            <Typography variant="h6" component="div">
              {currentUser.displayName || currentUser.phoneNumber}
            </Typography>
            <CustomizedChip label={user?.wallet?.points ?? 0} icon={<DiamondIcon />} color="secondary" variant="filled" size="small" />
          </div>
        </Toolbar>
        <Divider />
        <List className='content'>
          {
            MENU_ITEMS_LIST.map((item) => (
              <MenuIconRenderer
                key={item.key}
                value={item.key} // added value prop to remove console warning
                path={item.path}
                label={item.label}
                icon={item.icon}
              />
            ))
          }
          {OneMarketSupportButton}
        </List>

        <Divider />
        {displayMarketPlaceButton() && MarketPlaceButton}

        <Button
          variant="outlined"
          color="primary"
          size="large"
          startIcon={<Logout />}
          fullWidth
          sx={{ marginBottom: 2, marginTop: 1 }}
          className="drawer-action-btn"
          onClick={userLogout}
        >
          Log Out
        </Button>
        <VersionNumber />
      </Box>
    )
  }

  return (
    <div>
      <Button
        variant="text"
        color="inherit"
        onClick={toggleDrawer(true)}
        aria-label="open drawer"
        sx={{ borderRadius: '50%', padding: '9px', minWidth: 'unset' }}
        {...menuButtonProps}
      >
        <Menu fontSize="large" sx={{ padding: '0 !important' }} />
      </Button>
      <Drawer
        variant="temporary"
        ModalProps={{ keepMounted: true }}
        anchor={'right'}
        open={drawerOpen}
        onClose={toggleDrawer(false)}>
        <If condition={!isEmptyOrNil(user)}>
          <Then>
            {RenderUserList(user)}
          </Then>
          <Else>
            {RenderAnonymousList()}
          </Else>
        </If>
      </Drawer>

      <SignupDrawer
        open={signupDrawerOpen}
        setOpen={setSignupDrawerOpen}
        entrance={SignupEntrance.USER_LOGIN}
      />
    </div>
  )
}