import React, { useMemo } from 'react';
import { Link } from 'react-router-dom';
import { If } from 'react-if';
import { head, pathOr, pipe } from 'ramda';
import { CardContent, Typography, Card, CardActionArea, Box } from '@material-ui/core';
import { isArray } from 'class-validator';

// src
import { Merchandise, Merchant, MerchantCoupon } from '../../services/models';
import PlaceholderImage from "../../assets/images/merchant-placeholder.png";

import { isEmptyOrNil } from '../../utils';
import { StyledGreenChip } from './styledComponents';
import { useCardStyles } from './styles';
import MerchandisePrice from '../MerchantMenu/MerchandisePrice';

const getCardBackgroundSrc = (merchant: Merchant, merchandise: Merchandise) => {
  if (!isEmptyOrNil(merchandise.photos)) return isArray(merchandise.photos) ? head(merchandise.photos) : merchandise.photos
  if (!isEmptyOrNil(merchant.heroPhoto)) return merchant.heroPhoto

  return PlaceholderImage
}

const ClassifiedMerchandiseCard = ({ merchandise, merchant, chipText }: { merchandise: Merchandise, merchant: Merchant, chipText?: string }) => {
  const classes = useCardStyles();
  const cardBackgroundSrc = getCardBackgroundSrc(merchant, merchandise)

  const promotionCoupon: MerchantCoupon | undefined = pipe(
    pathOr({}, ['promotions', 0]),
    pathOr(undefined, ['coupons', 0]),
  )(merchant);

  return (
    <Card elevation={3} className={classes.root}>
      <CardActionArea
        className="card-action-container"
        component={Link}
        to={{
          pathname: `/${merchant.username}/item/${merchandise.id}`,
          state: { from: `/${merchant.username}`, sanMateo: true },
        }}
      >
        <img className="card-background-img" src={cardBackgroundSrc} />
        <div className='overlay' />

        <Box className="card-highlight-text-container">
          <If condition={!isEmptyOrNil(chipText)}>
            <StyledGreenChip size="small" label={chipText} variant="filled" />
          </If>
        </Box>

        <CardContent className="card-content">
          <div className='merchandise-title-container'>
            <Typography className="card-title" title={merchandise.name} component="div" variant="h6">
              {merchandise.name}
            </Typography>

            <Typography className="card-subtitle" component="div" variant="h6">
              {merchant.officialName}
            </Typography>

            <div className="card-info-container">
              <MerchandisePrice merchandise={merchandise} coupon={promotionCoupon} />
            </div>
          </div>
        </CardContent>
      </CardActionArea>
    </Card>
  )
}

export default ClassifiedMerchandiseCard