import React, { useState, useContext, useRef } from "react";
import { CardHeader, createStyles, makeStyles, Theme, ToggleButton, ToggleButtonGroup, Typography } from "@material-ui/core";
import { Schedule, Error } from "@material-ui/icons";
import { Else, If, Then } from "react-if";
import { rgba } from "polished";

import { Merchant, OrderType } from "../../services/models";
import { displayTime, isKioskDevice, isStoreOpen } from '../../utils';
import { AppContext } from "../../context/AppContext";
import RenderWaitTime from "../common/WaitTime";

import ScheduleOrderDrawer from "./ScheduleOrderDrawer";
import MerchantAddress from "../MerchantInfo/MerchantAddress";

type PickupOptionsProps = {
  merchant: Merchant,
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    standardOptionsContainer: {
      margin: theme.spacing(2),
      marginTop: theme.spacing(1),
      display: 'flex',
      flexDirection: 'row',
      justifyContent: 'space-between',
      alignItems: 'center',

      '& .withIcon': {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        '& .MuiCardHeader-root': {
          padding: 0,
        },
        '& .MuiSvgIcon-root': {
          marginRight: 5,
        }
      },
      '& .needPickupTime': {
        color: theme.palette.primary.main,
        fontWeight: 700,
      },
      '& .MuiToggleButton-root': {
        paddingTop: 4,
        paddingBottom: 4,
      },
      '& .MuiTypography-root': {
        lineHeight: 1.2,
      },
    },
    addressContainer: {
      marginLeft: theme.spacing(2),
      marginRight: theme.spacing(2),
    },
    noScheduleOptionsContainer: {
      margin: theme.spacing(2),
      marginTop: theme.spacing(1),
      display: 'flex',
      flexDirection: 'row',
      alignItems: 'center',
      '& .MuiSvgIcon-root': {
        marginRight: 5,
      },
      '& .MuiCardHeader-root': {
        padding: 0,
      },
      '& .MuiTypography-root': {
        lineHeight: 1.2,
      },
    },
    kioskOptionsContainer: {
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      marginBottom: theme.spacing(1),
      '& > div': {
        marginLeft: theme.spacing(1)
      }
    },
  }),
);

const RenderKioskModeOption = ({ merchant }: Omit<PickupOptionsProps, 'isKioskMode'>): React.ReactElement => {
  const classes = useStyles();

  return (
    <div className={classes.kioskOptionsContainer}>
      <Schedule color="secondary" />
      {merchant &&
        <RenderWaitTime merchant={merchant} withIcon={false} />
      }
    </div>
  )
}

const RenderNoScheduleOptions = ({ merchant }: Omit<PickupOptionsProps, 'isKioskMode'>): React.ReactElement => {
  const classes = useStyles();

  return (
    <div className={classes.noScheduleOptionsContainer}>
      <Schedule color="secondary" />
      {merchant &&
        <CardHeader
          title={
            <Typography variant="subtitle2" color='textSecondary' component="div">
              Pick up time
            </Typography>
          }
          subheader={
            <RenderWaitTime
              merchant={merchant}
              withIcon={false}
              typographProps={{
                variant: "subtitle2",
              }}
            />
          }
        />
      }
    </div>
  )
}

const RenderStandardPickupOptions = ({ merchant }: Omit<PickupOptionsProps, 'isKioskMode'>): React.ReactElement => {
  const classes = useStyles();
  const [option, setOption] = useState('now')
  const [isOpen, setIsOpen] = useState(false)
  const { pickupTime, setPickupTime } = useContext(AppContext)
  const scheduleToggleRef = useRef(null);
  const isValidPickupTime = isStoreOpen(merchant, pickupTime ? new Date(pickupTime) : new Date())

  const handleOption = (event: React.MouseEvent<HTMLElement>, newOption: string | null) => {
    if (newOption !== null) {
      setOption(newOption)
      if (newOption === "schedule") {
        setIsOpen(true)
      } else if (newOption === "now") {
        setPickupTime(undefined)
      }
    } else if (event.currentTarget === scheduleToggleRef.current) {
      setIsOpen(true)
    }
  }

  const onChangeDate = (d: Date) => {
    setPickupTime(d || undefined)
  }

  const onCloseScheduleDrawer = () => {
    if (pickupTime) {
      setOption("schedule")
    } else {
      setOption("now")
    }
  }

  return (
    <div className={classes.standardOptionsContainer}>
      <div className="withIcon">
        <If condition={!isValidPickupTime}>
          <Then>
            <Error color="error" />
            <Typography variant="subtitle2" className="needPickupTime" component="div">
              Pickup time required
            </Typography>
          </Then>
          <Else>
            <Schedule color="primary" />
            <CardHeader
              title={
                <Typography variant="subtitle2" color='textSecondary' component="div">
                  Pick up time
                </Typography>
              }
              subheader={
                <Typography variant="subtitle2" component="div">
                  {pickupTime
                    ? displayTime(pickupTime, true)
                    : <RenderWaitTime
                      merchant={merchant}
                      withIcon={false}
                      short={true}
                      typographProps={{
                        variant: "subtitle2",
                      }}
                    />}
                </Typography>
              }
            />
          </Else>
        </If>
      </div>
      <div>
        <ToggleButtonGroup
          value={option}
          color="primary"
          size="small"
          exclusive
          onChange={handleOption}
          aria-label="Pick up options">
          <ToggleButton color="primary" value="now" aria-label="pick up now">
            Now
          </ToggleButton>
          <ToggleButton ref={scheduleToggleRef} color="primary" value="schedule" aria-label="schedule for later">
            Schedule
          </ToggleButton>
        </ToggleButtonGroup>

        <ScheduleOrderDrawer
          merchant={merchant}
          defaultValue={pickupTime}
          open={isOpen}
          setOpen={setIsOpen}
          onSchedule={onChangeDate}
          onClose={onCloseScheduleDrawer} />
      </div>
    </div>
  )
}

const PickupOptions = ({ merchant }: PickupOptionsProps): React.ReactElement => {
  const classes = useStyles();
  const isKioskModeEnabled = isKioskDevice()
  const { orderType } = useContext(AppContext)

  return isKioskModeEnabled || orderType === OrderType.DINEIN
    ? <RenderKioskModeOption merchant={merchant} />
    : <>
      <div className={classes.addressContainer}>
        <MerchantAddress merchant={merchant} typographProps={{ variant: "subtitle2", color: "textSecondary" }} />
      </div>
      <If condition={merchant.orderingConfig?.orderAhead}>
        <Then><RenderStandardPickupOptions merchant={merchant} /></Then>
        <Else><RenderNoScheduleOptions merchant={merchant} /></Else>
      </If>
    </>
}

export default PickupOptions