
import React from "react"
import { Card, List, ListItem, ListItemIcon, ListItemText, Typography, makeStyles, createStyles, Theme, CardContent, Divider } from "@material-ui/core"
import { Place, Phone, Description, Schedule } from "@material-ui/icons"
import { If } from "react-if"
import ReactMarkdown from "react-markdown"

import { Merchant } from "../../services/models"
import BottomDrawer from "../../bos_common/src/components/BottomDrawer"
import MerchantStoreHours from "./MerchantStoreHours"

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      marginTop: theme.spacing(3),
      marginBottom: theme.spacing(3),
      paddingRight: theme.spacing(1),
      '& .MuiDivider-root:last-child': {
        display: 'none',
      },
      '& .phoneLink': {
        color: theme.palette.primary.main,
      }
    },
  }),
);

const MerchantInfoDrawer = (props: {
  open: boolean,
  setOpen: (open: boolean) => void,
  merchant: Merchant,
}): React.ReactElement => {

  const classes = useStyles()
  const { officialName, category, address, phoneNumber, description, hours } = props.merchant
  const title = (
    <div>
      <Typography variant="h5" component="div">{officialName}</Typography>
      <Typography variant="subtitle1" color="textSecondary" component="div">
        {category}
      </Typography>
    </div>
  )

  return (
    <BottomDrawer {...props} title={title} >
      <CardContent>
        <Card className={classes.root} elevation={3}>
          <List>
            <If condition={address}>
              <>
                <ListItem>
                  <ListItemIcon>
                    <Place color="primary" />
                  </ListItemIcon>
                  <ListItemText primary={address} />
                </ListItem>
                <Divider variant="inset" component="li" />
              </>
            </If>
            <If condition={Boolean(hours)}>
              <>
                <ListItem>
                  <ListItemIcon>
                    <Schedule color="primary" />
                  </ListItemIcon>
                  <ListItemText primary={<MerchantStoreHours merchant={props.merchant} />} />
                </ListItem>
                <Divider variant="inset" component="li" />
              </>
            </If>
            <If condition={phoneNumber}>
              <>
                <ListItem>
                  <ListItemIcon>
                    <Phone color="primary" />
                  </ListItemIcon>
                  <ListItemText>
                    <a href={`tel:${phoneNumber}`} className="phoneLink"> {phoneNumber}</a>
                  </ListItemText>
                </ListItem>
                <Divider variant="inset" component="li" />
              </>
            </If>
            <If condition={description}>
              <>
                <ListItem>
                  <ListItemIcon>
                    <Description color="primary" />
                  </ListItemIcon>
                  <ListItemText>
                    <ReactMarkdown>
                      {description || ''}
                    </ReactMarkdown>
                  </ListItemText>
                </ListItem>
                <Divider variant="inset" component="li" />
              </>
            </If>
          </List>
        </Card>
      </CardContent>
    </BottomDrawer>
  )
}

export default MerchantInfoDrawer