import React from 'react';
import Typography from '@material-ui/core/Typography';
import { Else, If, Then } from 'react-if';
import renderPrice from '../../bos_common/src/components/Price';
import { Merchandise } from '../../bos_common/src/types/MerchandiseType';
import { isMerchandisePromoted } from '../../utils';
import { MerchantCoupon } from '../../bos_common/src/types/MerchantCouponType';
import { isCouponValidOnMerchandise } from '../../bos_common/src/services/CouponUtils';
import { getDiscountedAmount } from '../../services/cartUtils';

export interface MerchandisePriceProps {
  merchandise: Merchandise;
  coupon?: MerchantCoupon;
}

export const renderMerchadisePrice = (merchandise: Merchandise, coupon: MerchantCoupon | undefined, quantity = 1, currency = 'USD', ): string => {

  const value = merchandise.price * quantity;

  // We only render the lower price if there is a promotion and the merchandise is included
  if (!coupon || !coupon.promotion || !isCouponValidOnMerchandise(merchandise, coupon)) {
    return renderPrice(value, currency);
  }

  const newValue = value - getDiscountedAmount(value, coupon);
  return renderPrice(newValue, currency);
};

export const MerchandisePrice = (props: Readonly<MerchandisePriceProps>): JSX.Element => {
  const { merchandise, coupon } = props;

  return (
    <If condition={isMerchandisePromoted(merchandise, undefined, coupon)}>
      <Then>{() =>
        <>
          <Typography style={{ fontSize: '1rem', fontWeight: 700, color: '#FF8E25' }}>{renderMerchadisePrice(merchandise, coupon)}</Typography>&nbsp;
          <Typography style={{ fontSize: '.75rem', fontWeight: 400, color: '#9CA5A8', textDecoration: 'line-through', }}>{renderPrice(merchandise.price)}</Typography>&nbsp;
        </>}
      </Then>
      <Else>
        {renderMerchadisePrice(merchandise, coupon)}
      </Else>
    </If>
  );
};


export default MerchandisePrice;
