import React from 'react';
import Fab from '@material-ui/core/Fab';
import ArrowForward from '@material-ui/icons/ArrowForward';
import { makeStyles } from '@material-ui/core/styles';

export interface ReviewFABProps {
  onNext: () => void;
  onDone: () => void;
  disableNext?: boolean;
}

const useStyles = makeStyles(() => ({
  root: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',

    '& .reviewFab': {
      '&:first-child': {
        marginRight: '10px'
      }
    },
  }
}));

export const ReviewFAB = (props: Readonly<ReviewFABProps>): JSX.Element => {
  const classes = useStyles();
  const { onNext, onDone, disableNext } = props;

  const handleClickNext = () => {
    onNext();
  }

  const handleClickDone = () => {
    onDone();
  }

  return (
    <div className={classes.root}>
      <Fab
        variant="extended"
        color="primary"
        className='reviewFab'
        disabled={disableNext}
        onClick={handleClickNext}
      >
        <ArrowForward sx={{ mr: 1 }} />
        Next
      </Fab>
      <Fab
        variant="extended"
        color="secondary"
        className='reviewFab'
        sx={{ backgroundColor: '#FFF', color: '#E34D28' }}
        onClick={handleClickDone}
      >
        Done
      </Fab>
    </div>
  );
}

export default ReviewFAB;
