import React, { useMemo } from 'react';
import useAxios from 'axios-hooks';
import { If } from 'react-if';
import LazyLoad from 'react-lazyload';
import {
  Avatar,
  Box,
  Button,
  CardContent,
  CardHeader,
  createStyles,
  Drawer,
  Fab,
  makeStyles,
  Theme,
  Typography,
  useMediaQuery,
} from '@material-ui/core';
import { Map } from '@material-ui/icons';

import SimpleLoader from '../bos_common/src/components/SimpleLoader';
import { FullscreenPaper } from '../bos_common/src';
import { FabContainer } from '../bos_common/src/components/FabContainers';

// src
import { Merchant } from '../services/models';
import { getSortedMerchantsListBasedOnStatus, isEmptyOrNil } from '../utils';
import PageHeader from '../components/common/PageHeader';
import { useHistory } from 'react-router';
import AppLogo from "../assets/icons/logo.svg"
import PromotionsDrawer from '../components/common/PromotionsDrawer';
import ReviewEarnRewards from '../assets/images/review-earn-rewards.png';
import { useAppDispatch, useAppSelector } from '../redux/hooks';
import { isEarnRewardsDrawerShown } from '../redux/slice/review/reviewSelector';
import { hideEarnRewardsDrawerUI } from '../redux/slice/review/reviewAction';
import StoresListDetailed from '../components/Stores/StoresListDetailed';


const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      minHeight: '100vh',
      backgroundColor: '#f5faff !important',
      overflow: 'auto',
      marginTop: 4,

      '& .marketplace-header': {
        padding: theme.spacing(2),
        paddingBottom: 0,
      },
    }
  })
)

const PageTitleMarkup = (
  <>
    <CardHeader
      sx={{ padding: `8px 8px 8px 0` }}
      title={
        <Typography variant="h6" component="div" className="store-name">
          One Market
        </Typography>
      }
      avatar={<Avatar color="secondary" alt={"One Market"} src={AppLogo} variant="square" />}
      subheader={(
        <Typography variant="caption" component="div">
          Buy Local, Earn Rewards
        </Typography>
      )}
    />
  </>
)

const MerchantListHeader = ({ data, loading }: { data?: Merchant[], loading: boolean }) => (
  <div className='marketplace-header'>
    <div>
      <Typography variant="h6" component="div">Merchants Nearby</Typography>
      <Typography variant="caption" component="div" color="textSecondary">
        {"Local businesses that you'll love"}
      </Typography>
    </div>

    <If condition={isEmptyOrNil(data) && !loading}>
      <CardContent><p>No merchant Exists Nearby</p></CardContent>
    </If>
  </div>
)

const HomePage = (): React.ReactElement => {
  const classes = useStyles();
  const history = useHistory();
  const reduxDispatch = useAppDispatch();
  const earnRewardsDrawerShown: boolean = useAppSelector(isEarnRewardsDrawerShown);

  const [{ data: fetchedMerchantList, loading }] = useAxios<Merchant[]>({
    url: '/users/nearby/merchants',
    method: "GET"
  })

  const isMobile = useMediaQuery((theme: Theme) => theme.breakpoints.down('sm'));

  const handleClickSearchMap = () => {
    history.push('/discovery', { from: location.pathname });
  }

  const handleClickConfirm = () => {
    reduxDispatch(hideEarnRewardsDrawerUI());
  }

  const handleCloseEarnRewardsDrawer = () => {
    reduxDispatch(hideEarnRewardsDrawerUI());
  }

  // Sort Merchant: Show online merchants at start
  const merchants = useMemo(() => getSortedMerchantsListBasedOnStatus(fetchedMerchantList || []), [fetchedMerchantList]);

  return (
    <div className={"container"}>
      <PageHeader
        theme="dark"
        title={PageTitleMarkup}
      />
      <PromotionsDrawer />

      <FullscreenPaper>
        <div className={classes.root}>
          <SimpleLoader loading={loading} />
          <MerchantListHeader data={merchants} loading={loading} />
          <Box sx={{ px: 2, my: 1 }}>
            <StoresListDetailed merchants={merchants} />
          </Box>
        </div>

        <FabContainer
          alignment='right'
          fabStyle={{ padding: "24px 16px 16px" }}
          fadeTop={isMobile}>
          <Fab
            color="primary"
            aria-label="Search"
            variant="extended"
            onClick={handleClickSearchMap}>
            <Map sx={{ mr: 1 }} />
            Search &amp; Map View
          </Fab>
        </FabContainer>
        <Drawer
          anchor={'bottom'}
          open={earnRewardsDrawerShown}
          onClose={handleCloseEarnRewardsDrawer}
          style={{
            borderTopRightRadius: '8px',
            borderTopLeftRadius: '8px',
          }}
        >
          <div style={{ display: 'flex', flexDirection: 'column', placeItems: 'center', margin: '1rem' }}>
            <img src={ReviewEarnRewards} alt="Earn rewards" />
            <Typography style={{ fontSize: '1.25rem', fontWeight: 400, textAlign: 'center', marginTop: '1rem', marginBottom: '1rem' }}>
              Congratulations! You&apos;ve made your review.
            </Typography>
            <Button variant="contained" color="primary" style={{ width: '100%' }} onClick={handleClickConfirm}>Done</Button>
          </div>
        </Drawer>
      </FullscreenPaper>
    </div>
  )
}

export default HomePage;
