type CreditCardPaymentProps = {
  merchant?: Merchant,
  currency: string,
  amountInCents: number,
  checkPrerequisite: () => boolean,
  setError: (_: string) => void,
  createOrder: (_?: string, __?: string) => Promise<void>,
  processing: boolean,
  setProcessing: (_: boolean) => void,
}

const CreditCardPaymentForm = (props: CreditCardPaymentProps) => {
  const { merchant, currency, amountInCents, checkPrerequisite, setError, createOrder, processing, setProcessing } = props;
  const { user, token, authenticating } = useContext(UserContext);
  const { cartType } = useContext(AppContext);
  const [name, setName] = useState<string>(user?.displayName || '');
  const [setupIntentSecret, setSetupIntentSecret] = useState<string>('');
  const stripe = useStripe();
  const elements = useElements();

  useEffect(() => {
    if (authenticating) return;
    if (merchant) {
      // Create PaymentIntent over Stripe API
      setProcessing(true);
      const config = token ? { headers: getAuthHeaders(token) } : {}
      axios.post<PaymentAttempResponse>(
        '/users/attempt-to-pay',
        {
          merchantId: merchant?.id || '',
          currency: currency,
          amount: amountInCents,
        },
        config,
      ).then((response) => {
        setProcessing(false);
        if (response?.data.setupIntent && response?.data.setupIntent.client_secret) {
          // Collect payment methods before pay
          setSetupIntentSecret(response?.data.setupIntent.client_secret);
        } else if (response?.data.paymentIntent) {
          // Pay directly
          // TODO: need to show saved payment method
          createOrder(response?.data.paymentIntent.id, name).then();
        } else {
          setError('Payment service is not available at this moment, please try again later');
        }
      }).catch((err) => {
        setProcessing(false);
        setError(err.message);
      });
    }
  }, [authenticating]);

  const payWithStripe = async (ev: React.FormEvent) => {
    setError("");
    ev.preventDefault();

    if (!checkPrerequisite()) {
      return;
    }
    if (name.trim().length < 1) {
      setError("Name is required");
      return;
    }

    const card = elements?.getElement(CardElement)
    setProcessing(true);

    if (!stripe || !elements || !card) {
      setError("Could not connect to payment services")
      setProcessing(false)
      return;
    }

    const { setupIntent, error } = await stripe.confirmCardSetup(setupIntentSecret, {
      payment_method: {
        card: card,
        billing_details: {
          name: name,
        },
      }
    });

    if (!setupIntent || setupIntent.status !== 'succeeded') {
      setError('Could not save your card, please try again');
      setProcessing(false);
      return;
    }

    // convert method to intent.
    const config = token ? { headers: getAuthHeaders(token) } : {}
    try {
      const payload = await axios.post<PaymentIntent>(
        '/users/save-setup-intent-and-pay',
        {
          merchantId: merchant?.id || '',
          setupIntent,
          currency,
          amount: amountInCents,
        },
        config,
      );

      if (payload.status === 200) {
        await createOrder(payload?.data.id, name);
      }

    } catch (err) {
      const msg = getAPIErrorMessage(err);
      setError(`Payment failed: ${msg}`);
      setProcessing(false);
      console.log("[error]", err);
    }
  };

  const options = {
    style: {
      base: {
        color: "#32325d",
        fontFamily: '"Helvetica Neue", Helvetica, sans-serif',
        fontSmoothing: "antialiased",
        fontSize: "16px",
        "::placeholder": {
          color: "#aab7c4",
        },
      },
      invalid: {
        color: "#fa755a",
        iconColor: "#fa755a",
      },
    },
  };

  return (
    <div className="credit-card-form-container">
      <Typography variant='subtitle1' component='div' style={{ marginBottom: 16 }}>
        Credit/Debit card payment
      </Typography>
      <form onSubmit={payWithStripe}>
        <If condition={!!setupIntentSecret}>
          <div className="credit-card-inputs">
            <div className="credit-card-inputs-row">
              <input
                type="text"
                name="name"
                placeholder="Name"
                autoComplete="cardholder"
                className="credit-card-input credit-card-name-element"
                value={name}
                onChange={e => setName(e.target.value)}
              />
            </div>

            <div className="credit-card-inputs-row">
              <CardElement
                className="credit-card-input credit-card-pay-element"
                options={options}
              />
            </div>
          </div>
        </If>
        <Fab
          color="primary"
          variant="extended"
          type="submit"
          disabled={processing || !setupIntentSecret || !stripe}
          className="credit-card-pay-btn"
        >
          <CreditCard fontSize="large" />
          &nbsp;{isCartFromAnOpenTabOrder(cartType) ? 'Pay' : 'Place Order'} {renderPrice(amountInCents/100)}
        </Fab>
      </form>
    </div>
  );
};


type IntegratedPaymentMethodsProps = {
  merchant?: Merchant,
  currency: string,
  amountInCents: number,
  checkPrerequisite: () => boolean,
  setError: (_: string) => void,
  createOrder: (_?: string, __?: string) => Promise<void>,
  setProcessing: (_: boolean) => void,
}
const IntegratedPaymentMethods = (props: IntegratedPaymentMethodsProps) => {
  const { merchant, currency, amountInCents, checkPrerequisite, setError, createOrder, setProcessing } = props;
  const stripe = useStripe();
  const [paymentIntentId, setPaymentIntentId] = useState('');
  const [paymentRequest, setPaymentRequest] = useState<PaymentRequest>();
  const [hide, setHide] = useState(false);

  const handlePaymentMethodReceived = useCallback(async (ev: PaymentRequestPaymentMethodEvent) => {

    // early exist without the proper data
    if (!stripe || !paymentIntentId) return;
   // if (!checkPrerequisite()) return;

    // Confirm the PaymentIntent without handling potential next actions (yet).
    setProcessing(true);
    const payload = await axios.post<PaymentIntent>(
      '/users/confirm-payment-intent',
      {
        merchantId: merchant?.id || '',
        payment_intent_id: paymentIntentId,
        payment_method: ev.paymentMethod.id
      },
    );

    const paymentIntent = (payload.status === 200) ? payload.data : null;

    if (!paymentIntent
      || paymentIntent.status === "requires_payment_method"
      || paymentIntent.status === "requires_action") {
      // Report to the browser that the payment failed, prompting it to
      // re-show the payment interface, or show an error message and close
      // the payment interface.
      ev.complete('fail');
      setHide(true);
      setProcessing(false);
      setError('Payment failed, please try using cards directly');
    } else {
      // Report to the browser that the confirmation was successful, prompting
      // it to close the browser payment method collection interface.
      ev.complete('success');
      // The payment has succeeded.
      await createOrder(paymentIntent?.id, ev.payerName)
      setProcessing(false);
    }
  }, [stripe, paymentIntentId]);

  useEffect(() => {
    if (stripe && merchant) {
      // for google pay, apple pay
      const pr = stripe.paymentRequest({
        country: 'US',
        currency: currency.toLowerCase(),
        total: {
          label: 'Total',
          amount: amountInCents,
        },
        requestPayerName: true,
        requestPayerEmail: false,
      });
      pr.canMakePayment().then((result) => {
        if (result) {
          setPaymentRequest(pr);
        }
      })

      axios.post<PaymentIntent>(
        '/users/create-payment-intent',
        {
          merchantId: merchant.id,
          currency: currency,
          amount: amountInCents,
        },
      ).then((response) => {
        if (response.status === 200) {
          setPaymentIntentId(response.data.id || '');
        }
      })
    }
  }, [stripe, merchant]);

  useEffect(() => {
    if (paymentRequest && paymentIntentId) {
      paymentRequest.on('paymentmethod', handlePaymentMethodReceived);
    }
  }, [paymentRequest, paymentIntentId])

  if (!merchant || !paymentRequest || isEmptyOrNil(paymentIntentId) || hide) return null

  return (
    <div className='integrated-payments-container' style={{ marginTop: 32 }}>
      <Typography variant='subtitle1' component='div' style={{ marginBottom: 16 }}>
        Or use other payment methods
      </Typography>
      <PaymentRequestButtonElement options={{ paymentRequest }} />
    </div>
  )
}


type StripePaymentFormProps = {
  onSuccessSubmit: (order: Order) => void
}

type PaymentAttempResponse = {
  setupIntent?: SetupIntent,
  paymentIntent?: PaymentIntent,
}

const StripePaymentForm = (props: StripePaymentFormProps): React.ReactElement => {
  const appContext = useContext(AppContext);
  const { cart, merchantConfig, tip, coupon, reFetchMerchandisesForCart, cartType } = appContext;
  const { user, token, refreshUser } = useContext(UserContext);
  const [error, setError] = useState<string>('');
  const [processing, setProcessing] = useState(false);
  const [phone, setPhoneNumber] = useState<string>(user?.phoneNumber || '');

  const classes = useStyles();

  const amount = getTotalPrice({ cart, tip, coupon, orderingConfig: merchantConfig?.merchant.orderingConfig });
  // minimum amount accepted by stripe is 0.50
  const amountInCents = Math.round(amount * 100);
  const currency = "USD";

  useEffect(() => {
    // fetch latest merchandise data to
    // 1/ make sure prices are accurate
    // 2/ make sure inventories are accurate
    if (merchantConfig?.merchant) {
      reFetchMerchandisesForCart();
    }
  }, [])

  const checkPrerequisite = () => {
    if (phone.length < 11) {
      setError("Phone number is required");
      return false;
    }

    if (!isCartFromAnOpenTabOrder(cartType) && checkCartItemsAvailability({ cart, merchantConfig })) {
      setError("Some items in your cart are sold out, please go back and check")
      return false;
    }

    return true;
  }

  const sendPromotionSmsNotification = (orderId: string) => {
    const promotion = getLiveMerchantPromotion(merchantConfig?.merchant);
    promotion && axios.post(`orders/${orderId}/promotion-sms`);
  };

  const sendOrderSubscription = (orderId: string, phoneNumber: string) => {
    axios.post(`/orders/${orderId}/subscribers`, {
      phoneNumber
    }).then(() => {
      sendPromotionSmsNotification(orderId)
    })
  }

  const createOrder = async (paymentIntentId?: string, displayName?: string) => {
    try {
      setProcessing(true);
      const results = await createOrderService({
        appContext,
        paymentIntentId: paymentIntentId,
        name: displayName,
        user,
        token
      });
      setProcessing(false);

      if (results.status === 200) {
        sessionStorage.setItem(LOCALSTORAGE_APP_KEYS.USER_PHONE_NUMBER, phone);
        displayName && sessionStorage.setItem(LOCALSTORAGE_APP_KEYS.USER_DISPLAY_NAME, displayName);

        setError('')

        if (!isEmptyOrNil(user)) {
          refreshUser()
        } else if (phone && phone.length >= 11) {
          // only send notifications if user has not signed i
          sendOrderSubscription(results.data.id, `+${phone}`)
        }
        props.onSuccessSubmit(results.data)
      } else {
        setError(`Payment failed, please try again later`);
      }
    } catch (err) {
      const message = getAPIErrorMessage(err) || `Payment failed, please try again later`;
      setError(message);
      console.log(err)
    }
  }

  return (
    <div className={classes.root}>
      <SimpleLoader loading={processing} />
      <If condition={error}>
        <Alert severity='warning'>{error}</Alert>
      </If>
      <Grid container justifyContent="center">
        <Grid item xs={12} sm={6} lg={4}>
          {isEmptyOrNil(user?.phoneNumber) && (
            <div className='phone-number-container'>
              <Typography variant='subtitle1' component='div'>
                Your phone number *
              </Typography>
              <div className='phone-input'>
                <PhoneInput
                  inputClass="phoneInput"
                  country={'us'}
                  onlyCountries={['us', 'pk', 'cn', 'id', 'in']}
                  value={phone}
                  specialLabel=""
                  placeholder="+1 (234) 567-8910"
                  onChange={p => {
                    setPhoneNumber(p);
                    setError("");
                  }}
                />
                <Typography variant='subtitle1' component='div' className='helper-text'>
                  to receive order updates
                </Typography>
              </div>
            </div>
          )}
        </Grid>
      </Grid>
      <Grid container justifyContent="center">
        <Grid item xs={12} sm={6} lg={4}>
          <CreditCardPaymentForm
            merchant={merchantConfig?.merchant}
            currency={currency}
            amountInCents={amountInCents}
            checkPrerequisite={checkPrerequisite}
            setError={setError}
            createOrder={createOrder}
            processing={processing}
            setProcessing={setProcessing}
          />
        </Grid>
      </Grid>
      {/* <Grid container justifyContent="center">
        <Grid item xs={12} sm={6} lg={4}>
          <IntegratedPaymentMethods
            merchant={merchantConfig?.merchant}
            currency={currency}
            amountInCents={amountInCents}
            checkPrerequisite={checkPrerequisite}
            setError={setError}
            createOrder={createOrder}
            setProcessing={setProcessing}
          />
        </Grid>
      </Grid> */}
    </div>
  );
}

export default StripePaymentForm