import React, { useContext, useMemo } from 'react';
import { Theme, createStyles, makeStyles, } from '@material-ui/core/styles';
import { Card, CardMedia, Typography, useMediaQuery, Button } from '@material-ui/core';
import { pluck, includes } from "ramda";
import { Else, If, Then } from 'react-if';

// src
import MerchandiseModifiers from '../../bos_common/src/components/Merchandise/MerchandiseModifiers';
import renderPrice from '../../bos_common/src/components/Price';
import MerchandiseNote from '../../bos_common/src/components/Merchandise/MerchandiseNote';
import { AppContext, CartItemType } from '../../context/AppContext';
import QuantityInput from '../MerchantMenu/QuantityInput';
import { enableOrderItemNote, isMerchandisePromoted, withFallbackPath } from '../../utils';
import { ShoppingCartContext } from '../../context/ShoppingCart/ShoppingCartContext';
import { MerchantPromotion } from '../../bos_common/src/types/MerchantPromotionType';
import { MerchantCoupon } from '../../services/models';
import { renderMerchadisePrice } from '../MerchantMenu/MerchandisePrice';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      position: 'relative',
      marginTop: theme.spacing(1),
      marginBottom: theme.spacing(2),
      padding: theme.spacing(1),
      '& .OutOfOrderInfo': {
        position: 'absolute',
        left: 0,
        top: '50%',
        marginTop: `-${theme.spacing(2)}`,
        width: '100%',
        textAlign: "center",
        backgroundColor: theme.palette.grey[100],
        color: theme.palette.grey[500],
        zIndex: 10,
        paddingTop: theme.spacing(1),
        paddingBottom: theme.spacing(1),
      }
    },
    row1: {
      display: 'flex',
      '& .details': {
        position: 'relative',
        display: 'flex',
        flexDirection: 'column',
        flex: "1 1 auto",
        justifyContent: "space-evenly",
        [theme.breakpoints.up('sm')]: {
          justifyContent: 'space-between',
        },
      },
      '& .MuiButtonBase-root': {
        display: 'flex',
        justifyContent: 'center',
      },

      '& .cover': {
        width: 100,
        height: 100,
        marginRight: theme.spacing(1),
        [theme.breakpoints.up('sm')]: {
          width: 150,
          height: 150,
        },
      }
    },
    row2: {
      display: 'flex',
      alignItems: 'baseline',
      justifyContent: 'space-between',
      marginTop: theme.spacing(1),
      '& .quantityDisabled': {
        padding: theme.spacing(.5),
      },
    },

  }),
);

export default function CartItemCard(props: {
  item: CartItemType,
  coupon?: MerchantCoupon,
  readOnly?: boolean
}): React.ReactElement {
  const isMobile = useMediaQuery((theme: Theme) => theme.breakpoints.down('sm'))
  const { merchantConfig, addItemToCart, removeItemFromCart, featureFlags } = useContext(AppContext)
  const { soldoutItems } = useContext(ShoppingCartContext)

  const classes = useStyles();
  const { item, coupon, readOnly = false } = props;

  const currentInventory = item?.inventory?.dailyCurrentStock

  const isItemOutOfStock = useMemo(() => {
    return includes(item.id, pluck('id', soldoutItems))
  }, [soldoutItems, item])

  const RenderItemQtyAndPrice = () => {
    const onChangeQuantity = (newQuantity: number, change: number) => {
      if (change > 0) {
        const newItem = { ...props.item };
        newItem.quantity = change;

        addItemToCart(newItem, merchantConfig?.merchant);
      } else if (change < 0) {
        for (let i = change; i < 0; i++) {
          removeItemFromCart({ merchandiseId: item.id, modifiersSignature: item.modifiersSignature });
        }
      }
    }
    return (
      <div className={classes.row2}>
        <If condition={readOnly}>
          <Then>
            <Button variant="outlined" disabled className="quantityDisabled">
              {item.quantity}
            </Button>
          </Then>
          <Else>
            <QuantityInput
              quantity={item.quantity}
              minQuantity={0}
              maxQuantity={currentInventory}
              onQuantityChange={onChangeQuantity} />
          </Else>
        </If>
        <Typography variant="body2" component="div">
          <If condition={(coupon) && isMerchandisePromoted(item, undefined, coupon)}>
            <Then>{() =>
              <>
                <Typography variant='caption' style={{ textDecoration: 'line-through', marginRight: '.5rem' }}>{renderPrice(item.price  * item.quantity)}</Typography>
                {renderMerchadisePrice(item, coupon, item.quantity)}
              </>}
            </Then>
            <Else>
              {renderPrice(item.price  * item.quantity)}
            </Else>
          </If>
        </Typography>
      </div>
    )
  }

  const photo = item.photos && item.photos.length > 0 && item.photos[0] != null
    ? item.photos[0]
    : null;

  const CardMediaEnhanced: any = useMemo(() => withFallbackPath(CardMedia, photo), [photo]);

  return (
    <Card className={classes.root} elevation={3}>
      <If condition={!readOnly && isItemOutOfStock}>
        <Typography component="div" variant="subtitle1" className="OutOfOrderInfo">
          This item is currently out of stock
        </Typography>
      </If>
      <div className={classes.row1}>
        {photo && (
          <CardMediaEnhanced
            component="img"
            className="cover"
            src={`${photo}_small`}
            title={item.name}
            alt='cart-item-img'
          />
        )}
        <div className="details">
          <div>
            <Typography component="div" variant="subtitle1" style={{ lineHeight: 1.2, marginBottom: '4px' }}>
              {item.name}
            </Typography>
            <MerchandiseModifiers modifiers={item.modifiers} modifiersDisplayOrder={item.modifiersDisplayOrder} />
            <If condition={enableOrderItemNote(featureFlags)}>
              {item.note && <MerchandiseNote note={item.note} />}
            </If>
          </div>
          <If condition={!isMobile}>
            <RenderItemQtyAndPrice />
          </If>
        </div>
      </div>
      {/* render the Qty and Price below the first row on mobile */}
      <If condition={isMobile}>
        <RenderItemQtyAndPrice />
      </If>
    </Card>
  );
}