import React, { ReactElement, useContext, useEffect, useMemo } from "react"
import {
  Toolbar,
  Typography,
  CardContent,
  LinearProgress,
  makeStyles,
  Theme,
  createStyles,
  CardMedia,
  Divider,
  Fab,
  SwipeableDrawer,
} from "@material-ui/core"
import assert from "assert"

import { UserContext } from "../../bos_common/src/context/UserContext"
import { ColoredPaper } from "../../bos_common/src/components/Papers"
import PhoneSignup from '../../bos_common/src/components/PhoneSignup';
import OneMarketPointString from '../../bos_common/src/components/OneMarketPointString';
import { repeatAudio } from "../../bos_common/src"

import { Merchant, Order } from "../../services/models"
import HeartIllustration from '../../assets/images/illustration_heart_light.png'
import RenderWaitTime from "../common/WaitTime"
import { isPickupASAP } from "../../bos_common/src/services/orderUtils"
import { isNotificationSoundEnabled } from "../../utils"

export enum SignupEntrance {
  FAVORTIE = 'favorite',
  EARNED_POINTS = 'earned-points',
  SAVE_ORDER = 'save-order',
  NEW_ORDER = 'new-order',
  PLACE_ORDER = 'place-order',
  USER_LOGIN = 'user_login'
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    drawer: {
      '& .MuiPaper-root': {
        overflowY: "unset",
        borderRadius: "20px 20px 0 0",

        '&:before': {
          content: "''",
          position: "absolute",
          top: -15,
          left: "50%",
          transform: "translateX(-50%)",
          pointerEvents: "none",
          width: "130px",
          height: "6px",
          borderRadius: "50px",
          background: "rgba(255, 255, 255, 0.5)"
        },
        '& .toolbarWrapper': {
          padding: `${theme.spacing(3)} ${theme.spacing(2)}`,
          borderRadius: "20px 20px 0 0 !important", // to override inline style
        },
      },
      '& .toolbar': {
        '& .drawerTitle': {
          width: "100%",
          textAlign: "center",
          color: "#364245",
          fontSize: 20,
        },
      },
      '& .signupImageWrapper': {
        textAlign: "center",
        paddingBottom: "10px",
      },
      '& .signupImage': {
        height: 200,
        objectFit: "cover",
        margin: "auto",
      },
      '& .content': {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        '& .drawerCTA': {
          display: 'flex',
          justifyContent: 'center',
          flexDirection: 'column',
        },
      }
    },
  }),
);

const SignupCTA = (props: { redirectTo?: string }) => {
  return (
    <>
      <Typography gutterBottom variant="subtitle1" color="textSecondary" component="div" textAlign="center">
        Enter your phone number to get started
      </Typography>
      <div style={{ marginLeft: '18px' }}>
        <PhoneSignup redirectTo={props.redirectTo} />
      </div>
    </>
  );
}

const LeaveOrSignupCTA = (props: { redirectTo?: string, onLeave?: () => void }) => {
  return (
    <>
      <SignupCTA redirectTo={props.redirectTo} />
      <Divider style={{ marginTop: '24px', marginBottom: '16px' }} />
      <Fab color="primary" variant="extended" onClick={props.onLeave}>
        Leave anyways
      </Fab>
    </>
  )
}

const SignupDrawer = (props: {
  open: boolean,
  setOpen: (open: boolean) => void,
  entrance: SignupEntrance,
  onCTA?: () => void,
  order?: Order,
  merchant?: Merchant,
  forceShow?: boolean,
  redirectAfterSignup?: string
}) => {
  const classes = useStyles()
  const { user, authenticating } = useContext(UserContext)

  const { entrance, open, setOpen, forceShow, order, onCTA, redirectAfterSignup, merchant } = props

  const playNotificationSound = isNotificationSoundEnabled(user);
  const noShow = (user || authenticating) && (!forceShow)

  useEffect(() => {
    if (entrance === SignupEntrance.EARNED_POINTS && open && !noShow) {
      const notifAudio = document.getElementById('app_earn_1m_points_audio') as HTMLAudioElement;
      notifAudio && playNotificationSound && repeatAudio(notifAudio, 1)
    }
  }, [])

  const RenderEarnedPointsTitle = () => {
    if (!order) return null;
    return (
      <>
        Congratulations, you have earned {order.oneMarketPoints}{' '}
        <OneMarketPointString />. Sign up to claim
      </>
    );
  }

  const RenderUserLoginSignupTitle = () => {
    return <>
      <Typography variant="h5" color="primary" sx={{ fontWeight: 600 }}>Buy Local, Get Rewards!</Typography>
      <Typography variant="subtitle2" color="textSecondary">
        {'Sign up or login to your One Market account to support your local businesses & get rewards!'}
      </Typography>
    </>
  }

  const getConfig = (): { title: ReactElement | string, imageSrc: string, CTA?: ReactElement } => {
    switch (entrance) {
      case SignupEntrance.FAVORTIE:
        return {
          title: 'Sign up to favorite stores',
          imageSrc: '../favorite-shops.png',
          CTA: <SignupCTA redirectTo={redirectAfterSignup} />
        }
      case SignupEntrance.USER_LOGIN:
        return {
          title: <RenderUserLoginSignupTitle />,
          imageSrc: '../delivery-guy-handing-box.png',
          CTA: <PhoneSignup showActionBtn />
        }
      case SignupEntrance.EARNED_POINTS:
        assert(order && order.oneMarketPoints > 0)
        return {
          title: <RenderEarnedPointsTitle />,
          imageSrc: '../earned-1mpoints.png',
          CTA: <SignupCTA redirectTo={redirectAfterSignup} />
        }
      case SignupEntrance.SAVE_ORDER:
        return {
          title: 'Keep link to your order?',
          imageSrc: '../lose-order-access.png',
          CTA: <LeaveOrSignupCTA redirectTo={redirectAfterSignup} onLeave={onCTA} />
        }
      case SignupEntrance.NEW_ORDER:
        return {
          title: 'Order received!',
          imageSrc: HeartIllustration,
          CTA: (order && isPickupASAP(order) ? <RenderWaitTime order={order} /> : undefined)
        }
      case SignupEntrance.PLACE_ORDER:
        return {
          title: 'Sign up to pay later',
          imageSrc: '../mobile-order.png',
          CTA: <SignupCTA redirectTo={redirectAfterSignup} />
        }
      default:
        return {
          title: 'Sign up to collect points, save orders, and favorite shops',
          imageSrc: '../mobile-order.png',
          CTA: <SignupCTA redirectTo={redirectAfterSignup} />
        }
    }
  }
  const config = useMemo(() => getConfig(), [entrance, order, merchant]);

  if (noShow) {
    return null;
  }

  const toggleDrawer = (open: boolean) => (
    event: React.KeyboardEvent | React.MouseEvent,
  ) => {
    if (
      event?.type === 'keydown' &&
      ((event as React.KeyboardEvent).key === 'Tab' ||
        (event as React.KeyboardEvent).key === 'Shift')
    ) {
      return;
    }

    setOpen(open)
  }

  return (
    <SwipeableDrawer
      variant="temporary"
      ModalProps={{
        keepMounted: false,
      }}
      anchor={'bottom'}
      open={open}
      onOpen={toggleDrawer(true)}
      onClose={toggleDrawer(false)}
      className={classes.drawer}
    >
      <ColoredPaper
        role="presentation"
        className="toolbarWrapper"
      >
        <CardMedia className="signupImageWrapper" title={"sign up to 1m.app"}>
          <img src={config.imageSrc} className="signupImage" />
        </CardMedia>

        <Toolbar className="toolbar">
          <Typography variant="h6" className="drawerTitle" component="div">
            {config.title}
          </Typography>
        </Toolbar>
        <CardContent className='content'>
          {authenticating && <LinearProgress />}
          {
            config?.CTA &&
            <div className="drawerCTA">
              {config.CTA}
            </div>
          }
        </CardContent>
      </ColoredPaper>
    </SwipeableDrawer>
  )
}

export default SignupDrawer