import React, { useContext, useState } from "react";
import {
  Button,
  createStyles,
  Fab,
  makeStyles,
  Theme,
  Typography,
  useTheme,
  useMediaQuery,
} from "@material-ui/core";
import { pathOr } from "ramda";
import { useLocation } from "react-router-dom";
import { format } from "date-fns";
import { If } from "react-if";

// src
import { UserContext } from "../../bos_common/src/context/UserContext";
import { MerchantPromotion } from "../../bos_common/src/types/MerchantPromotionType";
import { isCouponValid, isValidExpiryDate } from "../../bos_common/src/services/CouponUtils";
import { Merchant } from "../../services/models";
import { isUserEligibleForPromotion } from "../../utils";
import PromotionIcon from "../../assets/icons/PromotionIcon";
import CustomDrawer from "../CustomDrawer";
import PromotionDialog from "./PromotionDialog";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      textAlign: "center",
      padding: `${theme.spacing(2)} ${theme.spacing(2)}`,

      "&.sticky": {
        zIndex: 10,
        position: "sticky",
        top: "68px",
        background: theme.palette.common.white,
      },

      "& .buttonWrapper": {
        padding: `${theme.spacing(1)} ${theme.spacing(2)}`,
      },

      "& .promotionButton": {
        borderRadius: theme.spacing(1),
        color: theme.palette.common.white,
        backgroundColor: "#F7932E",
        fontSize: "14px",

        "& img": {
          marginRight: theme.spacing(1),
        },

        "&:hover": {
          backgroundColor: "#F7932E",
        },

        "&.rounded": {
          borderRadius: 30,
        },
      },
    },
  })
);

type PromotionButtonProps = {
  merchant?: Merchant;
};

const PromotionButton = (props: PromotionButtonProps) => {
  const classes = useStyles();
  const theme = useTheme();
  const location = useLocation<{ promotion: MerchantPromotion }>()
  const { merchant } = props;
  let promotion = location.state?.promotion;
  let coupon = promotion?.coupons?.find(c => isCouponValid(c))

  if (!promotion) {
    promotion = pathOr({}, ['promotions', 0], merchant);
    coupon = pathOr({}, ['coupons', 0], promotion);
  }

  const [isPromotionDrawerOpen, setPromotionDrawerOpen] = useState(false);
  const [isPromotionDialogOpen, setPromotionDialogOpen] = useState(false);

  const { user } = useContext(UserContext);
  const isMobile = useMediaQuery((theme: Theme) =>
    theme.breakpoints.down("sm")
  );

  if (!(promotion && isUserEligibleForPromotion(promotion, user))) {
    return null
  }

  const DialogProps = {
    title: promotion.title,
    description: promotion.description,
    promotionImage: process.env.PUBLIC_URL + '/merchant-promotion-image.png',
  }

  const DialogContent = coupon && (
    <Typography fontSize={isMobile ? "14px" : "16px"} color={theme.palette.grey[600]}>
      Use <b>{coupon.code}</b> on your next order to claim the rewards!<br />
      <If condition={isValidExpiryDate(coupon.endDate)}>
        Valid until <b>{format(new Date(coupon.endDate), 'MMM d, p')}</b>
      </If>
    </Typography>
  )

  return isMobile ? (
    <div className={`${classes.root} sticky`}>
      <div className="promotionButton">
        <Button
          className="promotionButton"
          onClick={() => setPromotionDrawerOpen(true)}
        >
          {<PromotionIcon />}&nbsp; {promotion.oneliner}
        </Button>
      </div>
      <CustomDrawer {...DialogProps} isOpen={isPromotionDrawerOpen} setIsOpen={setPromotionDrawerOpen}>
        {DialogContent}
      </CustomDrawer>
    </div>
  ) : (
    <span className={classes.root}>
      <Fab
        variant="extended"
        className="promotionButton rounded"
        onClick={() => setPromotionDialogOpen(true)}
      >
        {<PromotionIcon />}&nbsp; {promotion.title}
      </Fab>
      <PromotionDialog {...DialogProps} isOpen={isPromotionDialogOpen} setOpen={setPromotionDialogOpen}>
        <>{DialogContent}</>
      </PromotionDialog>
    </span>
  );
};

export default PromotionButton;