import * as React from "react";
import { useMemo } from "react";
import { Theme, createStyles, makeStyles, useTheme } from "@material-ui/core/styles";
import {
  Card,
  CardActionArea,
  Fab,
  CardMedia,
  Typography,
} from "@material-ui/core";
import { Link as RouterLink } from "react-router-dom";
import { Add, ChevronRight } from "@material-ui/icons";
import { If, Then, Else } from "react-if";
import { filter, head, pathOr, pipe, propEq, reduce, defaultTo } from "ramda";

import MerchandiseDescription from "../../bos_common/src/components/Merchandise/MerchandiseDescription";
import { isOverInventoryLimit } from "../../bos_common/src/services/merchandiseUtils";

import {
  Merchandise,
  MerchandiseModifier,
  Merchant,
  MerchantCoupon,
} from "../../services/models";
import { AppContext, CartItemType } from "../../context/AppContext";
import {
  isEmptyOrNil,
  isKioskDevice,
  isMerchandiseAvailable,
  isMerchandiseStockLow,
  isMerchantOnline,
  isNotificationSoundEnabled,
} from "../../utils";
import {
  RenderSoldOut,
  RenderLowStockWarning,
} from "../common/MerchandiseHelper";
import { repeatAudio, withFallbackPath } from "../../bos_common/src/utils";
import { UserContext } from "../../bos_common/src/context/UserContext";
import MerchandisePrice from "./MerchandisePrice";
import { isCouponValidOnMerchandise } from "../../bos_common/src/services/CouponUtils";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: "flex",
      height: "100%",

      "& .card-action-area": {
        display: "flex",
        alignItems: "flex-start",
        padding: theme.spacing(1),

        "& .details": {
          display: "flex",
          flexGrow: 1,
          flexDirection: "column",
          justifyContent: "space-between",
          marginBottom: theme.spacing(1),
          marginLeft: theme.spacing(1),
          marginRight: theme.spacing(1),
          height: "100%",

          "& .menu-title": {
            marginBottom: theme.spacing(1),
          },
          "& .itemRow": {
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-between",
            alignItems: "flex-end",
            marginBottom: theme.spacing(1),
            "& .MuiButtonBase-root": {
              "& .MuiSvgIcon-root": {
                fill: "white",
              },
            },

            "& .price-container": {
              display: "flex",
              alignItems: "center",
            },
          },
        },

        "& .merchPhoto": {
          display: "flex",
          alignItems: "center",

          "& .cover": {
            width: 120,
            height: 120,

            [theme.breakpoints.up("md")]: {
              width: 200,
              height: 200,
            },
          },
        },
      },
    },
  })
);

export default function MenuItemCard(props: {
  merchandise: Merchandise;
  merchant: Merchant;
  modifiers: MerchandiseModifier[] | undefined;
  coupon?: MerchantCoupon;
}): React.ReactElement {
  const classes = useStyles();
  const { merchant, merchandise, modifiers, coupon } = props;
  const merchantUserName = merchant.username;
  const merchandiseId = merchandise.id;
  const merchandisePhoto = defaultTo(
    "",
    head(pathOr([], ["merchandise", "photos"], props))
  );

  const itemPageLink = {
    pathname: `/${merchantUserName}/item/${merchandiseId}`,
    state: { from: window.location.pathname },
  };
  const isKioskModeEnabled = isKioskDevice();

  const { addItemToCart, cart, setCoupon } = React.useContext(AppContext);

  const { user } = React.useContext(UserContext);
  const playNotificationSound = isNotificationSoundEnabled(user);

  const onAddToCartClick = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    e.preventDefault();
    e.stopPropagation();
    // Assume no modifiers if an item can be added to
    addItemToCart(
      { ...merchandise, quantity: 1 } as CartItemType,
      merchant
    );
    if (isCouponValidOnMerchandise(merchandise, coupon)) {
      setCoupon(coupon);
    }

    const notifAudio = document.getElementById(
      "app_add_to_cart_audio"
    ) as HTMLAudioElement;
    notifAudio && playNotificationSound && repeatAudio(notifAudio, 1);
  };

  const quantityUsedWithinCart = pipe(
    filter(propEq("id", merchandiseId)),
    reduce((acc: number, item: CartItemType) => acc + item.quantity, 0)
  )(cart);

  const willBeOverStockLimit = isOverInventoryLimit(
    merchandise,
    quantityUsedWithinCart + 1
  );

  const RenderCTA = () => {
    const theme = useTheme();

    return (
      <>
        <If condition={!isMerchandiseAvailable(merchandise)}>
          <Then>
            <RenderSoldOut />
          </Then>
          <Else>
            <If condition={merchandise.modifierTemplateId}>
              <Then>
                <Fab color="secondary" size="small" aria-label="enter">
                  <ChevronRight style={{ fill: theme.palette.primary.main }} />
                </Fab>
              </Then>
              <Else>
                <Fab
                  color="secondary"
                  size="small"
                  aria-label="add"
                  disabled={willBeOverStockLimit}
                  onClick={onAddToCartClick}
                >
                  <Add style={{ fill: theme.palette.primary.main }} />
                </Fab>
              </Else>
            </If>
          </Else>
        </If>
      </>
    );
  };

  const CardMediaEnhanced: any = useMemo(
    () => withFallbackPath(CardMedia, merchandisePhoto),
    [merchandisePhoto]
  );

  return (
    <Card className={classes.root} elevation={3}>
      <CardActionArea
        className="card-action-area"
        component={RouterLink}
        to={itemPageLink}
      >
        {!isEmptyOrNil(merchandisePhoto) && (
          <div className="merchPhoto">
            <CardMediaEnhanced
              className="cover"
              src={`${merchandisePhoto}_small`}
              title={merchandise.name}
              component="img"
              alt="merch-img"
            />
          </div>
        )}

        <div className="details">
          <div>
            <Typography
              component="div"
              variant={isKioskModeEnabled ? "h6" : "body1"}
              style={{ color: "black", fontWeight: 500, lineHeight: 1.2 }}
            >
              {merchandise.name}
            </Typography>
            <MerchandiseDescription merchandise={merchandise} />
          </div>

          <div className="itemRow">
            <Typography
              variant="subtitle1"
              color="black"
              component="div"
              className="price-container"
            >
              <MerchandisePrice merchandise={merchandise} coupon={coupon} />
              <If condition={isMerchandiseStockLow(merchandise)}>
                <RenderLowStockWarning />
              </If>
            </Typography>

            <If condition={isMerchantOnline(merchant)}>
              <RenderCTA />
            </If>
          </div>
        </div>
      </CardActionArea>
    </Card>
  );
}
