import React, { useContext, useEffect, useState } from "react";
import { useHistory } from "react-router";
import { useLocation, useParams } from "react-router-dom";
import { Fab, Badge, Theme, useMediaQuery, makeStyles, createStyles } from "@material-ui/core";
import { ShoppingCart } from "@material-ui/icons";
import { Else, If, Then } from "react-if";
import useAxios from "axios-hooks";
import qs from 'qs';

import { FullscreenPaper } from '../bos_common/src/components/Papers';
import { FabContainer } from "../bos_common/src/components/FabContainers";
import SimpleLoader from "../bos_common/src/components/SimpleLoader";
import { AppContext } from "../context/AppContext";
import { Merchant, MerchantQRCode, OrderType, QRCodeType } from "../services/models";
import { getTotalItems } from "../services/cartUtils";
import { captureClickEvent, EVENT_TYPES } from "../services/EventsTracking";

import PageHeader from "../components/common/PageHeader";
import MerchantMenu from "../components/MerchantMenu/MerchantMenu";
import OrderOptionsGroup from "../components/Order/OrderOptionsGroup";
import MerchantInfoHeader from "../components/MerchantInfo/MerchantInfoHeader";
import ShoppingCartDrawer from "../components/ShoppingCart/ShoppingCartDrawer";
import RenderOfflineMerchantDrawer from "../components/MerchantOfflineDrawer";
import { getOfflineMerchantMessage, isEmptyOrNil, isKioskDevice, isAddingItemToOpenTab } from "../utils";
import ShoppingCartContextProvider from "../context/ShoppingCart/ShoppingCartContextProvider";
import BackButton from "../components/common/BackButton";
import NoneExistPage from "./NoneExistPage";
import { VisitedMerchantContext } from "../context/VisitedMerchantContext/VisitedMerchantContext";

import MerchandiseDrawerPage from './MerchandiseDrawerPage'
import MerchantPromotionsDrawer from "../components/common/MerchantPromotionsDrawer";
import { useAppSelector } from "../redux/hooks";
import { getUser } from "../redux/slice/auth/authSelector";
import PromotionButton from "../components/Promotions/PromotionButton";

interface IMerchantPageParams {
  merchantUserName: string,
  merchandiseId?: string,
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    totalCartItems: {
      '& .MuiBadge-badge': {
        color: theme.palette.background.paper,
        fontWeight: "bold"
      }
    }
  })
);

const MerchantPage = (): React.ReactElement => {
  const history = useHistory()
  const location = useLocation<{ from: { pathname: string } }>();
  const user = useAppSelector(getUser);

  const classes = useStyles();

  const isKioskModeEnabled = isKioskDevice();
  const isMobile = useMediaQuery((theme: Theme) => theme.breakpoints.down('sm'));
  const { merchantUserName, merchandiseId } = useParams<IMerchantPageParams>()

  const { cart, orderType, setOrderType, cartType } = useContext(AppContext)
  const { setVisitedMerchant, isMerchantOrderingAvailable } = useContext(VisitedMerchantContext)
  const [showCart, setShowCart] = useState<boolean>(false)

  const [{ data: merchant, loading }] = useAxios<Merchant>(
    { url: '/merchants', params: { username: merchantUserName } }
  )
  const tableQueryParam = new URLSearchParams(location.search).get("tableid")

  useEffect(() => {
    if (loading) return
    merchant && setVisitedMerchant(merchant);

    if (!isEmptyOrNil(tableQueryParam) && merchant) {
      const tableQrObject = merchant?.qrCodes?.find((i: MerchantQRCode) => `${i.id}` === `${tableQueryParam}`)
      const oType = tableQrObject?.type === QRCodeType.Table ? OrderType.DINEIN : OrderType.PICKUP
      setOrderType(oType, tableQrObject);
    }
  }, [tableQueryParam, merchant, loading])

  if (!merchant && !loading) {
    return <NoneExistPage title={<>Merchant&nbsp;<b>{merchantUserName}</b>&nbsp;not found</>} />
  }

  const RenderMerchantFab = () => {
    const openShoppingCart = () => {
      if (isKioskModeEnabled) {
        setShowCart(true)
      } else {
        history.push({ pathname: "/cart", state: { from: `/${merchantUserName}` } })
      }
      captureClickEvent({
        category: EVENT_TYPES.VISITED_SHOPPING_CART,
        data: { merchant, user }
      })
    }

    return (
      <FabContainer
        alignment='right'
        fabStyle={{ padding: "24px 16px 16px" }}
        fadeTop={isMobile}>
        <div>
          <If condition={!isMobile}>
            <PromotionButton merchant={merchant} />
          </If>
          <Fab
            disabled={!isMerchantOrderingAvailable}
            variant="extended"
            color="primary"
            onClick={openShoppingCart}
          >
            <If condition={isMerchantOrderingAvailable}>
              <Then>
                <>
                  <Badge
                    badgeContent={getTotalItems(cart)}
                    className={classes.totalCartItems}
                    sx={{ fontWeight: "bold", color: "#fff", marginRight: "8px" }}>
                    <ShoppingCart />
                  </Badge>
                  {'Shopping Cart'}
                </>
              </Then>
              <Else>{getOfflineMerchantMessage(orderType)}</Else>
            </If>
          </Fab >
        </div>
      </FabContainer>
    )
  }

  /**
  * On click header back event handler.
  */
  const handleBack = (): void => {
    const params = qs.parse(location.search, { ignoreQueryPrefix: true })
    if (params.sanMateo === 'true') {
      history.replace('/sanmateo')
      return
    }

    location.state?.from
      ? history.goBack()
      : history.push(`/`);
  }

  return (
    <div className="container">
      {!isKioskModeEnabled && (
        <PageHeader
          title={isAddingItemToOpenTab(cartType) ? "Add Items to Order" : !loading && <OrderOptionsGroup merchant={merchant} />}
          leftChild={<BackButton onBack={handleBack} />}
        />)}
      <SimpleLoader loading={loading} />
      {merchant &&
        <FullscreenPaper>
          <MerchantInfoHeader merchant={merchant} />
          <MerchantMenu merchant={merchant} />
          <RenderOfflineMerchantDrawer merchant={merchant} />
          <RenderMerchantFab />
          <If condition={isKioskModeEnabled}>
            <ShoppingCartContextProvider>
              <ShoppingCartDrawer showCart={showCart} setShowCart={setShowCart} />
            </ShoppingCartContextProvider>
          </If>
          <MerchandiseDrawerPage
            merchant={merchant}
            merchandiseId={merchandiseId}
          />
          <MerchantPromotionsDrawer merchant={merchant} />
        </FullscreenPaper>
      }
    </div >
  );
}

export default MerchantPage;