import React from 'react'
import { Chip, createStyles, makeStyles, PropTypes, Theme } from '@material-ui/core'

const useStyles = makeStyles(() => createStyles({
  root: {
    gap: 3,
    lineHeight: 1,
    borderRadius: '7px !important',
    '& .MuiChip-label': {
      paddingLeft: '3px',
      paddingRight: '5px',
    },

    '&.MuiChip-filled': {
      color: "white"
    }
  }
}))

type ChipColor = Exclude<PropTypes.Color, 'inherit'>;

type Props = {
  label: React.ReactNode | string,
  variant?: 'outlined' | 'filled',
  size?: "medium" | "small",
  color?: ChipColor,
  disabled?: boolean,
  icon?: React.ReactElement<any, string | React.JSXElementConstructor<any>>
}

const CustomizedChip = ({ label, color = 'warning' as ChipColor, variant = 'outlined', icon, size = 'medium', disabled = false }: Props) => {
  const classes = useStyles()

  return (
    <Chip
      variant={variant}
      color={color}
      label={label}
      icon={icon}
      size={size}
      disabled={disabled}
      classes={{
        root: classes.root
      }}
    />
  )
}

export default CustomizedChip
