import * as React from "react";
import { useState, useContext } from "react";
import { CardHeader, Typography, Avatar, Divider, IconButton, createStyles, makeStyles, Theme, Alert, Box, useMediaQuery } from "@material-ui/core"
import { InfoOutlined as InfoIcon } from "@material-ui/icons"
import { If, Then } from 'react-if'
import { pathOr } from "ramda";

import { Merchant, MerchantCoupon } from "../../services/models"
import FavoriteButton from "../common/FavoriteButton"
import RenderWaitTimeSlider from "../common/WaitTimeSlider";
import { withFallbackPath, getOfflineMerchantMessage, isMerchantOnline, isKioskDevice } from "../../utils";

import MerchantInfoDrawer from "./MerchantInfoDrawer";
import { AppContext } from "../../context/AppContext";
import TableIcon from "../../assets/icons/table-icon.svg";
import { rgba } from "polished";
import renderMerchantRewards from "../common/MerchantRewards";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      '& .filledIconButton': {
        '&.MuiButtonBase-root': {
          backgroundColor: `${rgba(theme.palette.primary.main, 0.15)}`,
          transition: "all 200ms linear",

          '&:hover': {
            backgroundColor: `${rgba(theme.palette.primary.main, 0.3)}`,
          }
        }
      },

      '& .MuiAvatar-root': {
        width: theme.spacing(8),
        height: theme.spacing(8),
      },
      '& .inDivider': {
        marginLeft: theme.spacing(2),
        marginRight: theme.spacing(2),
        borderBottom: `1px solid ${theme.palette.divider}`
      },
      '& .MuiCardHeader-action': {
        alignSelf: 'center',
      },
      '& .realTimeInfo': {
        padding: `${theme.spacing(1)} ${theme.spacing(2)}`
      },
      '& .offline-status': {
        margin: theme.spacing(2),
        '& .MuiTypography-subtitle1': {
          textTransform: 'uppercase',
          fontWeight: 'bold'
        }
      },
    }
  }),
);

type MerchantInforHeader = {
  merchant: Merchant,
}
const MerchantInfoHeader = ({ merchant }: MerchantInforHeader): React.ReactElement => {
  const classes = useStyles()
  const isMobile = useMediaQuery((theme: Theme) => theme.breakpoints.down("sm"));

  const { officialName, logo, category, orderingConfig } = merchant;
  const { orderType, tableQrCodeObject } = useContext(AppContext);
  const [showDetailInfo, setShowInfo] = useState<boolean>(false);

  const isMerchantOrderingAvailable = isMerchantOnline(merchant, orderType);
  const AvatarEnhanced: any = withFallbackPath(Avatar, logo);
  const isKioskModeEnabled = isKioskDevice();
  const rewards = renderMerchantRewards(merchant);
  const promotion = pathOr({}, ['promotions', 0], merchant);
  const coupon: MerchantCoupon = pathOr({}, ['coupons', 0], promotion);

  return (
    <div className={classes.root}>
      <CardHeader elevation={3}
        title={<Typography variant={isMobile ? "h6": "h5"} component="div">{officialName}</Typography>}
        avatar={<AvatarEnhanced color="secondary" alt={officialName} src={`${logo}_small`} variant="rounded" />}
        action={
          <>
            <If condition={!isKioskModeEnabled}>
              <IconButton color="primary" className="filledIconButton" onClick={() => setShowInfo(true)}>
                <InfoIcon />
              </IconButton>
            </If>
            &nbsp;
            <FavoriteButton merchant={merchant} />
          </>
        }
        subheader={
          <Box sx={{ display: 'flex', alignItems: 'center', flexWrap: 'wrap', lineHeight: 1 }}>
            <Typography variant={isMobile ? "caption" : "body1"} color="textSecondary" className="category-line">
              {category}{rewards ? <>&nbsp;&nbsp;{"|"}&nbsp;&nbsp;</> : ''}
            </Typography>
            <Typography variant={isMobile ? "caption" : "body1"} color="textSecondary" className="category-line">
              {rewards}
            </Typography>
          </Box>
        }
      />

      <Divider className="inDivider" />

      <If condition={!isMerchantOrderingAvailable}>
        <div className='offline-status'>
          <Alert severity="warning">{getOfflineMerchantMessage(orderType)}</Alert>
        </div>
      </If>

      <div className="realTimeInfo">
        <If condition={tableQrCodeObject?.identifier}>
          <Then>
            <Typography color="textSecondary" component="div" variant={isMobile ? "caption" : "body1"} sx={{ display: 'flex', alignItems: 'center' }}>
              <img src={TableIcon} width={24} />&nbsp;Table: {tableQrCodeObject?.identifier}
            </Typography>
          </Then>
        </If>
        <RenderWaitTimeSlider merchant={merchant} typographProps={{ variant: (isMobile ? "caption" : "body1"), color: "textSecondary" }} short />
      </div>
      <MerchantInfoDrawer merchant={merchant} open={showDetailInfo} setOpen={setShowInfo} />
    </div>
  )
}

export default MerchantInfoHeader;
