import React, { useContext, useMemo } from "react";
import {
  Theme,
  Typography,
  CardActionArea,
  Card,
  Box,
} from "@material-ui/core";
import { createStyles, makeStyles } from "@material-ui/styles";
import { pathOr, pipe, propOr, slice, split, without } from "ramda";
import { If } from "react-if";
import { rgba } from "polished";
import { Link, useLocation } from "react-router-dom";
import classNames from "classnames";

import { Merchant } from "../services/models";
import { getStoreStatusLabel, isEmptyOrNil } from "../utils";
import BannerImg from "../assets/images/merchant-placeholder.png";
import { AppContext } from "../context/AppContext";
import { MerchantPromotion } from "../bos_common/src/types/MerchantPromotionType";
import { ClassifiedEntry } from "../bos_common/src/types/ClassifiedType";
import MerchantCategoriesList from "./common/MerchantCategoriesList";
import { CardHighlightChip } from "./Classifieds/styledComponents";
import PromotionIcon from "../assets/icons/PromotionIcon";
import renderMerchantRewards from "./common/MerchantRewards";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      padding: 0,
      backgroundColor: 'transparent !important',
      height: '100%',

      '& .MuiCardActionArea-root': {
        height: 'inherit',
        paddingBottom: theme.spacing(1)
      },

      "& .heroPhotoWrapper": {
        position: "relative",
        marginBottom: theme.spacing(1),
        display: "flex",

        "& .overlay": {
          position: "absolute",
          top: 0,
          left: 0,
          width: "100%",
          height: "100%",
          borderRadius: theme.spacing(1),
          background: rgba(theme.palette.info.light, 0.6),
          zIndex: 10,
          display: "none",
          color: theme.palette.background.paper,
          fontWeight: 500,

          "&.show": {
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }
        },

        "& .highlights-container": {
          position: "absolute",
          padding: "10px 10px",

          "& .MuiChip-root": {
            marginBottom: "5px",

            "&:not(:last-child)": {
              marginRight: '5px'
            }
          }
        }
      },

      "& .heroPhoto": {
        maxWidth: "100%",
        width: "100%",
        borderRadius: theme.spacing(1),
        height: 170,
        objectFit: "cover",
        borderBottomLeftRadius: 0,
        borderBottomRightRadius: 0,
      },

      "& .cardContent": {
        display: "flex",
        justifyContent: "space-between",
        padding: `0 ${theme.spacing(0.5)}`,
      },

      "& .ratingWrapper": {
        display: "grid",
        alignItems: "flex-start",
        gridGap: "4px",
      },

      "& .rating": {
        color: theme.palette.info.light,
        fontSize: "12px",
        fontWeight: "bold",
        background: theme.palette.background.paper,
        padding: `${theme.spacing(0.2)} ${theme.spacing(0.3)}`,
        display: "flex",
        gap: "4px",
        borderRadius: "4px",
        boxShadow: "0px 3px 6px rgba(0, 0, 0, 0.07)",
      },

      "& .distance": {
        color: theme.palette.secondary.light,
        fontSize: "12px",
        fontWeight: 400,
      },

      "& .subtitleWrapper": {
        display: "flex",
        alignItems: "center",
        gap: "12px",
        color: theme.palette.secondary.light,
        fontWeight: 400,
        fontSize: "12px",
      },

      "& .highlightList": {
        display: "grid",
        marginTop: theme.spacing(2),

        "& .offer": {
          display: "flex",
          gap: theme.spacing(1.25),
          padding: `${theme.spacing(1)} ${theme.spacing(1.5)}`,
          fontSize: "12px",

          "& .tag": {
            background: theme.palette.background.paper,
            fontSize: "10px",
            fontWeight: 700,
            padding: `${theme.spacing(0.3)} ${theme.spacing(0.8)}`,
            borderRadius: "3px",
          },

          "&:first-child": {
            borderRadius: "8px 8px 0 0",
          },

          "&:last-child": {
            borderRadius: "0 0 8px 8px",
          },

          "&:nth-of-type(3n+1)": {
            background: rgba(theme.palette.primary.main, 0.15),
            color: theme.palette.primary.main,

            "& .tag": {
              color: theme.palette.primary.main,
            },
          },

          "&:nth-of-type(3n+2)": {
            background: rgba(theme.palette.secondary.main, 0.15),
            color: theme.palette.secondary.main,

            "& .tag": {
              color: theme.palette.secondary.main,
            },
          },

          "&:nth-of-type(3n+3)": {
            background: rgba("#22AC38", 0.15),
            color: "#22AC38",

            "& .tag": {
              color: "#22AC38",
            },
          },
        },
      },
    },
  })
);

type MerchantCardProps = {
  merchant: Merchant;
};

const MerchantCard = (props: MerchantCardProps) => {
  const { merchant } = props;

  const classes = useStyles();

  const { orderType } = useContext(AppContext);
  const location = useLocation();

  const categoriesList: string[] = useMemo(
    () =>
      pipe(
        propOr("", "category"),
        split(","),
        slice(0, 3),
        without([" ", ""])
      )(merchant),
    [merchant]
  );

  const storeStatusLabel = useMemo(() => getStoreStatusLabel(merchant, orderType), [merchant, orderType]);

  const renderLabels = () => {
    const labelsList = [];

    if (storeStatusLabel.canOrder) {
      labelsList.push(
        <span>{storeStatusLabel.label}</span>
      )
    }

    const rewards = renderMerchantRewards(merchant);
    if (rewards) {
      labelsList.push(rewards);
    }

    if (!isEmptyOrNil(labelsList)) {
      return labelsList
        .map<React.ReactNode>((t, index) => (
          <React.Fragment key={`${index}-${Math.random() * 100}`}>
            {t}
          </React.Fragment>
        ))
        .reduce((prev, curr) => [prev, "|", curr]);
    }

    return null;
  }

  const overlayClasses = {
    "overlay": true,
    "show": !storeStatusLabel.canOrder,
  }

  const highlightedPromotion = pathOr({}, ['promotions', 0], merchant) as MerchantPromotion
  const highlightedClassified = pathOr({}, ['classifieds', 0], merchant) as ClassifiedEntry
  const highlightedClassifiedLabel = pathOr('', ['details', 'name'], highlightedClassified)

  return (
    <Card className={classes.root}>
      <CardActionArea
        component={Link}
        to={{
          pathname: `/${merchant.username}`,
          state: { from: location.pathname },
        }}
      >
        <div className="heroPhotoWrapper">
          <img
            className="heroPhoto"
            src={isEmptyOrNil(merchant.heroPhoto) ? BannerImg : merchant.heroPhoto}
          />

          <div className={classNames(overlayClasses)}>{storeStatusLabel.label}</div>

          <div className="highlights-container">
            <If condition={!isEmptyOrNil(highlightedPromotion)}>
              <CardHighlightChip
                icon={<PromotionIcon />}
                label={highlightedPromotion.oneliner}
                color="secondary"
                size='small'
                variant='filled'
              />
            </If>
            <If condition={!isEmptyOrNil(highlightedClassifiedLabel)}>
              <CardHighlightChip
                icon={<PromotionIcon />}
                label={highlightedClassifiedLabel}
                color="secondary"
                size='small'
                variant='filled'
                sx={{ backgroundColor: "#0068FF" }}
              />
            </If>
          </div>
        </div>

        <Box sx={{ p: 1 }}>
          <div className="cardContent">

            <div>
              <Typography component="div" variant="h6">
                {merchant.officialName}
              </Typography>

              <div className="subtitleWrapper">
                {renderLabels()}
              </div>
            </div>

            {/* <div className="ratingWrapper">
          <If condition={!isEmptyOrNil(merchant.thirdPartyRating)}>
            <div className="rating">
              <Star sx={{ width: "15px", height: "15px", fill: "#F7CB2E" }} />{" "}
              {merchant.thirdPartyRating}
            </div>
          </If>

          <span className="distance">1.23 mi</span>
        </div> */}
          </div>

          <If condition={!isEmptyOrNil(categoriesList)}>
            <Box sx={{ pr: 2, mt: 2 }}>
              <MerchantCategoriesList categoriesList={categoriesList} />
            </Box>
          </If>
        </Box>
      </CardActionArea>
    </Card >
  );
};

export default MerchantCard;
