import { AxiosRequestConfig, AxiosResponse } from "axios";
import axios, { backendAPIURL } from "../bos_common/src/services/backendAxios";

export interface MerchantQueryParams {
  id?: string;
  email?: string;
  ownerId?: string;
  username?: string;
}

export const getMerchant = (queryParams: MerchantQueryParams, request: AxiosRequestConfig): Promise<AxiosRequestConfig> => {
  const url = new URL(`${backendAPIURL}/merchants`);
  Object.keys(queryParams).forEach((key) => url.searchParams.append(key, queryParams[key]));

  return axios({
    url: url.toString(),
    method: 'GET',
    headers: request.headers,
  });
}

export const getMerchantByNameQuery = (searchQuery: string, request: AxiosRequestConfig): Promise<AxiosResponse> => {
  const url = new URL(`${backendAPIURL}/merchants/search`);

  return axios({
    url: url.toString(),
    method: 'GET',
    headers: request.headers,
    params: {
      name: searchQuery,
      limit: 1000,
    }
  })
}

export default {
  getMerchantByNameQuery
}
