import React from "react";
import { SwipeableDrawer, Toolbar, makeStyles, Theme, createStyles, Typography } from "@material-ui/core";
import { If } from "react-if";
import { ColoredPaper } from "./Papers";

const useStyles = makeStyles((theme: Theme) =>
createStyles({
  drawer: {
    '& .MuiPaper-root': {
      overflowY: "unset",
      borderRadius: "8px", // to override inline style
      '&:before': {
        content: "''",
        position: "absolute",
        top: -15,
        left: "50%",
        transform: "translateX(-50%)",
        pointerEvents: "none",
        width: "130px",
        height: "6px",
        borderRadius: "50px",
        background: "rgba(255, 255, 255, 0.5)"
      },
    },
    '& .toolbar': {
      display: "flex",
      justifyContent: "space-between",
      marginTop: theme.spacing(1),
      paddingLeft: theme.spacing(2),
      '& .MuiTypography-root': {
        lineHeight: 1.2,
      },
    },
    '& .content': {
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
    }
  },
}),
);


const BottomDrawer = (props: {
  children: React.ReactElement,
  open: boolean,
  setOpen: (open: boolean) => void,
  onClose?: () => void,
  title?: React.ReactNode|string,
}): React.ReactElement => {
  const classes = useStyles()
  const {open, setOpen, onClose, title, children } = props;

  const toggleDrawer = (open: boolean) => (
    event: React.KeyboardEvent | React.MouseEvent,
  ) => {
    if (
      event && event.type === 'keydown' &&
      ((event as React.KeyboardEvent).key === 'Tab' ||
        (event as React.KeyboardEvent).key === 'Shift')
    ) {
      return;
    }

    setOpen(open)
    if (!open && onClose) {
      onClose()
    }
  }

  return (
    <SwipeableDrawer
      variant="temporary"
      ModalProps={{
        keepMounted: true,
      }}
      anchor={'bottom'}
      open={open}
      onOpen={toggleDrawer(true)}
      onClose={toggleDrawer(false)}
      className={classes.drawer}>
      <ColoredPaper
        className="toolbarWrapper"
        role="presentation">
        <If condition={!!title}>
          <Toolbar className="toolbar">
            <Typography variant="h6" component="div">
              {title}
            </Typography>
          </Toolbar>
        </If>
       {children}
      </ColoredPaper>
    </SwipeableDrawer>
  )
}

export default BottomDrawer