import React, { ChangeEvent, useMemo } from 'react';
import { TextField, styled, InputAdornment } from '@material-ui/core';
import { grey } from '@material-ui/core/colors';
import debounce from 'debounce'

const StyledTextField = styled(TextField)(({ theme }) => ({
  backgroundColor: 'white',
  borderRadius: 8,
  '& .MuiInputAdornment-root': {
    paddingRight: theme.spacing(1),
  },
  '& .MuiInputBase-input': {
    paddingLeft: theme.spacing(2),
    borderLeft: `1px solid ${grey[200]}`
  }
}))

interface StoreSearchBoxProps {
  onChangeQuery: (q: string) => void;
  placeholder?: string,
  defaultValue?: string,
  rightChild?: React.ReactNode,
  leftChild?: React.ReactNode,
}

// let timeoutId: NodeJS.Timeout | null = null;

export const StoreSearchBox = (props: StoreSearchBoxProps): JSX.Element => {
  const { onChangeQuery, placeholder = 'Search stores...', defaultValue = '', rightChild, leftChild } = props;

  React.useEffect(() => {
    return () => {
      debouncedHandler.clear();
    }
  }, []);

  const debouncedHandler = useMemo(() => debounce(onChangeQuery, 800), []);

  const handleInputChange = (e: ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value.trim();
    if (value.length > 2 || value.length === 0) {
      debouncedHandler(e.target.value);
    }
  }

  return (
    <StyledTextField
      fullWidth
      id="outlined-basic"
      placeholder={placeholder}
      variant="outlined"
      defaultValue={defaultValue}
      InputProps={{
        ...(leftChild && {
          startAdornment: (
            <InputAdornment position="start" >
              {leftChild}
            </InputAdornment>
          )
        }),
        ...(rightChild && {
          endAdornment: (
            <InputAdornment position="end" >
              {rightChild}
            </InputAdornment>
          )
        })
      }}
      onChange={handleInputChange}
    />
  );
}

export default StoreSearchBox;
