const CustomClusterRenderer = {
  render: function ({ count, position }: Cluster, stats: ClusterStats) {
    return new google.maps.Marker({
      position,
      icon: {
        url: MapMarker,
        labelOrigin: new google.maps.Point(25, 18),
        scaledSize: new google.maps.Size(50, 50),
      },
      label: {
        text: String(count),
        color: "rgba(255,255,255,0.9)",
        fontSize: "14px",
      },
      // adjust zIndex to be above other markers
      zIndex: Number(google.maps.Marker.MAX_ZINDEX) + count,
    });
  },
};

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      '&#embeded_map': {
        '& .marker-label': {
          backgroundColor: 'white',
          padding: 5,
          borderRadius: theme.spacing(1),
          fontSize: 10
        }
      }
    }
  }),
);

export const EmbededMap = (props: EmbededMapProps): JSX.Element => {
  const loader = new Loader({
    apiKey: "AIzaSyBgGQDhX_mo4htOJF8cINNECCW9Pf9_eCY",
  });
  const classes = useStyles()
  const history = useHistory()
  const { storeLocations } = props;
  const [map, setMap] = React.useState<google.maps.Map | null>(null);
  const [markers, setMarkers] = React.useState<google.maps.Marker[]>([]);
  const [markerclusterer, setMarkerClusterer] = React.useState<MarkerClusterer | null>(null);

  React.useEffect(() => {
    loader.load().then(initMap);
  }, []);

  React.useEffect(() => {
    if (!map) return;
    const markerList = updateMarkers(storeLocations);
    setMarkers([...markerList]);

    if (markerList.length > 0) {
      const position: LatLng = markerList[0].getPosition();
      map?.setCenter(position as google.maps.LatLng);
    }

    setMarkerClusterer(
      new MarkerClusterer({
        map,
        markers: markerList,
        renderer: CustomClusterRenderer
      }))
  }, [storeLocations, map]);

  const initMap = (): void => {
    const center = new google.maps.LatLng({ lat: 37.78444200981738, lng: -122.40296933567062 });
    const zoom = 10;

    const map = new google.maps.Map(document.getElementById("embeded_map") as HTMLElement, {
      center,
      zoom,
      styles: MapStyleArray,
      mapTypeControl: false,
      zoomControl: false,
      streetViewControl: false,
      minZoom: zoom - 5,
      restriction: {
        latLngBounds: {
          north: 50,
          south: 23,
          east: -66,
          west: -155,
        },
      },
    });

    setMap(map);

  }

  const updateMarkers = (storeLocations: StructuredAddress[]): google.maps.Marker[] => {
    let marker = null;
    const markerList: google.maps.Marker[] = [];

    for (let i = 0; i < markers.length; i++) {
      markers[i].setMap(null);
    }

    markerclusterer?.clearMarkers();
    setMarkers([]);

    storeLocations.forEach((item) => {
      marker = new google.maps.Marker({
        position: new google.maps.LatLng(item.lat as number, item.lng as number),
        icon: {
          url: MapMarkerStore,
          labelOrigin: new google.maps.Point(25, 55),
          scaledSize: new google.maps.Size(50, 50),
        },
        ...(item.title && {
          label: {
            text: item.title,
            color: 'black',
            fontSize: '12px',
            className: 'marker-label'
          }
        })
      });

      if (item.id) {
        marker.addListener('click', () => {
          history.push({ pathname: `/${item.id}`, state: { from: window.location.pathname } })
        })
      }

      marker.setMap(map);
      markerList.push(marker);
    });

    return markerList;
  }

  return (<div id="embeded_map" className={classes.root} style={{ height: `${props.height}px` }}></div>);
}

export default EmbededMap;
