import React, { useContext } from "react";
import { Alert } from "@material-ui/core";
import { Merchant, Order } from "../../services/models";
import { UserContext } from "../../bos_common/src/context/UserContext";
import PhoneSignup from '../../bos_common/src/components/PhoneSignup';
import OneMarketPointString from "../../bos_common/src/components/OneMarketPointString";

export default function Order1MPoints({order, merchant}: {
  order: Order,
  merchant?: Merchant
}): React.ReactElement | null {
  const { user, authenticating } = useContext(UserContext)
  const showSignup = !order.userId && !user

  if (authenticating) {
    return null
  }

  if (order.oneMarketPoints > 0) {
    if (showSignup) {
      return (
        <Alert severity="info" elevation={3}>
          Your purchase has earned &nbsp;{order.oneMarketPoints}{' '}
          <OneMarketPointString />. Sign up to claim those points, save order history and enable fast checkout
          <div style={{ marginTop: 8 }}>
            <PhoneSignup />
          </div>
        </Alert>
      )
    } else {
      if (user?.id === order.userId) {
        return (
          <Alert severity="success" elevation={3}>
            Your purchase has earned {order.oneMarketPoints}&nbsp;
            <OneMarketPointString />{' \u{1F389}'}
          </Alert>
        )
      }
    }
  } else {
    if (showSignup) {
      return (
        <Alert severity="info" elevation={3}>
          Sign up to save order history and enable fast checkout
          <div style={{ marginTop: 8 }}>
            <PhoneSignup />
          </div>
        </Alert>
      )
    }
  }

  return null
}