import React, { useEffect, useState } from 'react';
import {
  Box,
  createStyles,
  makeStyles,
  Theme,
  Typography,
  TypographyProps,
  useTheme
} from '@material-ui/core';
import { AlarmOn, Schedule } from '@material-ui/icons';
import useAxios from 'axios-hooks';
import { If, Then, Else } from 'react-if';

import { WaitTime } from '../../bos_common/src/types/WaitTimeType';
import { Merchant, Order } from '../../services/models';
import { pluralString } from '../../utils';
import { useFunctionOnInterval } from '../../bos_common/src';
import { WAIT_TIME_ON_ORDER_STATUS_INTERVAL } from '../../config/constants';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      '& .MuiTypography-root': {
        display: 'flex',
        alignItems: 'center',
        marginTop: "2px",
      },
      '& .wait-time-icon': {
        marginRight: 5,
        marginTop: "-2px",
      },
    },
    boxRoot: {
      display: 'flex',
      justifyContent: 'flex-start',
      alignItems: 'center',
      width: '100%',
      '& .wait-time-wrapper': {
        display: 'flex',
        alignItems: 'center',
        marginRight: 12,
        whiteSpace: 'nowrap',
      },
      '& .progress-bar-wrapper': {
        width: '100%',
        '& .progress-root': {
          display: 'block',
          width: '100%',
          maxWidth: '360px',
          height: 8,
          position: 'relative',
          borderRadius: 5,
          overflow: 'visible',
          background: '#E5E5E5',
          '& .progress-bar-indicator': {
            height: '100%',
            borderRadius: 5,
            mask: 'linear-gradient(#fff 0 0)',
            '&::before': {
              content: '""',
              borderRadius: 5,
              position: 'absolute',
              top: 0,
              left: 0,
              right: 0,
              bottom: 0,
              backgroundImage: 'linear-gradient(90deg, #00FF8A 0%, #FFF800 50.32%, #EA5514 100%)',
            }
          }
        }
      }
    },
  }),
);

type RenderWaitTimeProps = {
  merchant?: Merchant,
  order?: Order,
  withIcon?: boolean,
  short?: boolean,
  typographProps?: TypographyProps<"div">,
}
const RenderWaitTimeSlider = ({ merchant, order, withIcon = true, short = false, typographProps = {} }: RenderWaitTimeProps): React.ReactElement => {
  const classes = useStyles()
  const theme = useTheme()
  const [waitColor, setWaitColor] = useState<string>()

  const url = order
    ? `/orders/${order.id}/waitTime`
    : merchant
      ? `/merchants/${merchant.id}/waitTime`
      : undefined;

  const [{ data: waitTime, loading }, executeGet] = useAxios<WaitTime>(
    { url, method: 'get' },
    { manual: true }
  );

  const timerHandler = useFunctionOnInterval(executeGet, WAIT_TIME_ON_ORDER_STATUS_INTERVAL, true)

  useEffect(() => {
    if (waitTime && waitTime.readyTime) {
      if (timerHandler.current) {
        clearTimeout(timerHandler.current)
      }
    }

    if (waitTime) {
      if (waitTime.maxMinutes < 25) {
        setWaitColor(theme.palette.info.light)
      } else if (waitTime.maxMinutes < 40) {
        setWaitColor('#F8B62D')
      } else if (waitTime.maxMinutes <= 45) {
        setWaitColor('#F1840F')
      } else {
        setWaitColor('#EA5615')
      }
    }
  }, [waitTime])

  const getWaitTimeString = (minMinutes: number | undefined, maxMinutes: number | undefined) => {
    if (minMinutes == null || maxMinutes == null) {
      return 'n/a'
    } else {
      return (<span style={{ color: waitColor }}>{minMinutes}-{maxMinutes} mins</span>)
    }
  }

  return (
    <div className={classes.root}>
      <If condition={loading && !waitTime}>
        <Then>
          <Typography variant="subtitle1" color="textPrimary" component="div" {...typographProps}>
            {withIcon && <Schedule className='wait-time-icon' color='primary' />}
            Estimating&nbsp;...
          </Typography>
        </Then>
        <Else>
          <If condition={!!waitTime}>
            <Then>
              <Typography variant="subtitle1" color="textPrimary" component="div" {...typographProps}>
                {
                  waitTime?.readyTime
                    ? (<>
                      {withIcon && <AlarmOn className='wait-time-icon' color='primary' />}
                      {new Date(waitTime?.readyTime)}
                    </>)
                    : (waitTime?.priorOrdersInQueue && !short)
                      ? (<>
                        {withIcon && <Schedule className='wait-time-icon' color='primary' />}
                        {pluralString(waitTime.priorOrdersInQueue, 'order')} / {pluralString(waitTime.priorItemsInQueue, 'item')} in queue,
                        est.&nbsp;{getWaitTimeString(waitTime?.minMinutes, waitTime?.maxMinutes)}
                      </>)
                      : (<Box className={classes.boxRoot}>
                        <div className='wait-time-wrapper'>
                          {withIcon && <Schedule color="primary" className='wait-time-icon' />}
                          {getWaitTimeString(waitTime?.minMinutes, waitTime?.maxMinutes)}&nbsp;&nbsp;wait time
                        </div>
                        <div className='progress-bar-wrapper'>
                          <div className='progress-root'>
                            <div className='progress-bar-indicator' style={{ width: `${(waitTime?.maxMinutes ?? 0) / 60 * 100}%` }}></div>
                          </div>
                        </div>
                      </Box>)
                }
              </Typography>
            </Then>
          </If>
        </Else>
      </If >
    </div>
  )
}

export default RenderWaitTimeSlider;