import React, { useEffect, useState } from 'react'
import {
  Card,
  CardContent,
  Divider,
  Typography,
  Fab,
  Grid,
} from '@material-ui/core'
import { Check } from '@material-ui/icons'

import { displayOrderShortCode } from '../../bos_common/src/services/orderUtils'

import { Merchant, Order } from '../../services/models'
import AutoRedirectProgress from '../common/AutoRedirectProgress'
import RenderWaitTime from '../common/WaitTime'

import HeartIllustration from '../../assets/images/illustration_heart_light.png'
import { useStyles } from './styles'
import { getLiveMerchantPromotion } from '../../utils'
import PromotionDialog from '../Promotions/PromotionDialog'

interface GetPickUpDetailsProps {
  order: Order
}

const GetPickUpDetails = ({ order }: GetPickUpDetailsProps) => {
  return (
    <Card elevation={0}>
      <CardContent>
        <div className="info">
          <Typography variant="subtitle1" color="textSecondary" component="div">
            Order for
          </Typography>
          <Typography variant="h5">{order.userDisplayName}</Typography>
        </div>
      </CardContent>
      <Divider />
      <CardContent>
        <Typography variant="subtitle1" color="textSecondary" component="div">
          Pickup code
        </Typography>
        <Typography variant="h5" component="div">
          {displayOrderShortCode(order)}
        </Typography>
      </CardContent>
    </Card>
  )
}

interface KioskOrderStatusProps {
  order: Order
  onClose: () => void
  merchant?: Merchant
}

const KioskOrderStatusWideMode = ({ order, onClose, merchant }: KioskOrderStatusProps): React.ReactElement => {
  const classes = useStyles()

  const merchantPromotion = getLiveMerchantPromotion(merchant);
  const [ isPromotionDialogOpen, setPromotionDialogOpen ] = useState(false)

  useEffect(()=>{
    setPromotionDialogOpen(Boolean(merchantPromotion))
  },[merchantPromotion])

  return (
    <div className={classes.wideMode}>
      <div className="order-status-container">
        <div className="content">

          <Typography variant="h4">
            <Check />&nbsp;Order Received
          </Typography>

          <div className="heart-illustration-container">
            <img
              className="heart-illustration-image"
              src={HeartIllustration}
              alt="heart-illustration"
            />
          </div>

          <div className="pick-up-duration-text">
            <RenderWaitTime order={order} />
          </div>
        </div>
      </div>

      <div className="order-details-container">
        <div className="content">
          <AutoRedirectProgress
            onCompleteAction={onClose}
            autoRedirectDuration={5}
          />
          <GetPickUpDetails order={order} />
          <Fab
            color="primary"
            variant="extended"
            size="large"
            onClick={onClose}
            className="new-order-button"
          >
            Done
          </Fab>
        </div>
      </div>
      <PromotionDialog
        isOpen={isPromotionDialogOpen}
        setOpen={setPromotionDialogOpen}
        title={merchantPromotion?.title || ''}
        description={merchantPromotion?.description || ''}
      />
    </div>
  )
}

const KioskOrderStatusCompactMode = ({ order, onClose, merchant }: KioskOrderStatusProps): React.ReactElement => {
  const classes = useStyles()

  const merchantPromotion = getLiveMerchantPromotion(merchant)
  const [ isPromotionDialogOpen, setPromotionDialogOpen ] = useState(false)

  useEffect(()=>{
    setPromotionDialogOpen(Boolean(merchantPromotion))
  },[merchantPromotion])

  return (
    <Grid container spacing={3} className={classes.compactMode}>
      <Grid item xs={12} md={4}>
        <div className='order-status-container'>
          <div className="content">
            <Typography variant="h4">
              <Check fontSize="large"/>
              &nbsp;Order Received
            </Typography>
            <div className="heart-illustration-container">
              <img
                className="heart-illustration-image"
                src={HeartIllustration}
                alt="heart-illustration"
              />
            </div>
            <div className="pick-up-duration-text">
              <RenderWaitTime order={order} short={true} />
            </div>
          </div>
        </div>
      </Grid>

      <Grid item xs={12} md={8}>
        <div className='order-details-container'>
          <div className="content">
            <AutoRedirectProgress
              onCompleteAction={onClose}
              autoRedirectDuration={5}
            />
            <GetPickUpDetails order={order} />
            <Fab
              color="primary"
              variant="extended"
              size="large"
              onClick={onClose}
              className="new-order-button"
            >
              Done
            </Fab>
          </div>

          <PromotionDialog
            isOpen={isPromotionDialogOpen}
            setOpen={setPromotionDialogOpen}
            title={merchantPromotion?.title || ''}
            description={merchantPromotion?.description || ''}
          />
        </div>
      </Grid>
    </Grid>
  )
}

export { KioskOrderStatusCompactMode, KioskOrderStatusWideMode }
