import { Theme } from "@material-ui/core";

export const headerCustomStyle = (theme: Theme, backgroundColor: string) => {
  return {
    "& header": {
      paddingBottom: theme.spacing(4),
      background: backgroundColor,
      zIndex: 1,
      boxShadow: "unset",

      "& .toolbar": {
        "& .title": {
          textAlign: "left",
          color: "#fff",
        },

        "& .left": {
          "& svg": {
            fill: "#fff",
          },
        },

        "& .right": {
          "& svg": {
            fill: "#fff",
          },
        },
      },
    },
  };
};
