import React, { useState, useContext, useEffect } from "react";
import { NavigateNext } from "@material-ui/icons";
import { Alert, IconButton, makeStyles, Theme, createStyles, Button } from "@material-ui/core";
import PhoneInput from "react-phone-input-2";

// src
import PhoneVerificationDialog from "./PhoneVerificationDialog";
import { UserContext } from "../context/UserContext";
import 'react-phone-input-2/lib/material.css'
import { LOCALSTORAGE_APP_KEYS } from "../constants";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: "flex",
      flexDirection: "column",
      '& .signUpActionRow': {
        display: "flex",
        flexDirection: "row",
        width: ({ fullwidth }: any) => fullwidth ? '100%' : '270px',
        '& .phoneInput': {
          width: '100%',
          height: "36px",
        },
        '& .nextButton': {
          padding: "6px",
          marginLeft: "-37px",
        },
      },
      '& .MuiAlert-root': {
        backgroundColor: "transparent",
        padding: 0,
      },
    },
  }),
);

declare let window: any;
const countryPhoneLength: { [key: string]: number } = {
  'us': 10,
  'cn': 11,
  'pk': 10,
  'id': 11,
  'in': 10
}

const PhoneSignup = (props: {
  redirectTo?: string,
  fullwidth?: boolean,
  showActionBtn?: boolean,
}): React.ReactElement => {
  const { fullwidth, showActionBtn, redirectTo } = props;
  const classes = useStyles({ fullwidth })
  const cachedConsumerPhoneNumber = sessionStorage.getItem(LOCALSTORAGE_APP_KEYS.USER_PHONE_NUMBER)
  const [phone, _setPhone] = useState(cachedConsumerPhoneNumber || "")
  const [countryData, setCountryData] = useState()
  const [phoneSignupOpen, setPhoneSignupOpen] = useState(false)
  const [errorMsg, setErrorMsg] = useState("")
  const { setAuthenticating } = useContext(UserContext)

  const submitPhoneNumber = () => {
    if (isValid(phone, countryData)) {
      // auth0 api calls
      if (window?.webkit && window?.webkit.messageHandlers.authMessageHandler) {
        setAuthenticating(true)
        window?.webkit.messageHandlers.authMessageHandler.postMessage(phone);
      } else if (window?.authMessageHandler) {
        setAuthenticating(true)
        window.authMessageHandler.postMessage(phone)
      } else {
        setPhoneSignupOpen(true)
      }
    } else {
      setErrorMsg("Invalid phone number")
    }
  }

  const isValid = (phone: string, country: any) => {
    const countryCodeLength = country?.dialCode.length || 0;
    const countryCode = country?.countryCode as string || 'us';
    const countryPLength = countryPhoneLength[countryCode];
    if (phone.length - countryCodeLength >= countryPLength) {
      return true;
    }
    return false;
  }

  useEffect(() => {
    if (isValid(phone, countryData) && !cachedConsumerPhoneNumber) {
      submitPhoneNumber();
    }
  }, [phone, countryData])

  const setPhone = (p: string, country: any) => {
    _setPhone(p);
    setCountryData(country);
    setErrorMsg("");
  }

  return (
    <div className={classes.root}>
      <div className="signUpActionRow">
        <PhoneInput
          inputClass="phoneInput"
          country={'us'}
          onlyCountries={['us', 'pk', 'cn', 'id', 'in']}
          value={phone}
          specialLabel=""
          onEnterKeyPress={submitPhoneNumber}
          onChange={(phone, country) => setPhone(phone, country)}
        />
        <IconButton color="primary" aria-label="next" className="nextButton" onClick={submitPhoneNumber}>
          <NavigateNext />
        </IconButton>
      </div>
      {errorMsg && <Alert severity="error">{errorMsg}</Alert>}

      {showActionBtn && (
        <Button variant="contained" color="primary" size="large" onClick={submitPhoneNumber} fullWidth sx={{ marginTop: 1 }}>
          Sign Up/ Login
        </Button>
      )}
      <PhoneVerificationDialog phoneNumber={phone} open={phoneSignupOpen} setOpen={setPhoneSignupOpen} redirectTo={redirectTo} />
    </div>
  )
}

export default PhoneSignup