import React from "react";
import { Button, createStyles, Dialog, Theme, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      "& .MuiPaper-root": {
        padding: theme.spacing(5),
        borderRadius: "24px",
        maxWidth: 700,
        width: "100%",
        display: "grid",
        gridTemplateColumns: "auto 1fr",
        gridGap: "40px",

        "@media (max-width: 700px)": {
          textAlign: "center",
          gridTemplateColumns: "1fr",
        },

        "& .dialogImage": {
          maxWidth: "180px",
          width: "100%",

          "@media (max-width: 700px)": {
            margin: "auto",
          },
        },

        "& .dialogContent": {
          display: "flex",
          flexDirection: "column",
          justifyContent: "space-between",

          "& .heading": {
            color: "#364245",
            fontWeight: 600,
            fontSize: 24,
            margin: 0,
            marginBottom: 24,
          },

          "& .description": {
            color: theme.palette.grey[600],
            fontSize: "16px",
            lineHeight: 1.3,
          },

          "& .confirmButton": {
            borderRadius: "6px",
          },
        },
      },
    },
  })
);

type PromotionDialogProps = {
  isOpen: boolean;
  setOpen: (value: boolean) => void;
  title: string;
  description: string;
  promotionImage?: string;
  children?: React.ReactElement;
};

const PromotionDialog = (props: PromotionDialogProps) => {
  const {
    isOpen,
    setOpen,
    title,
    description,
    promotionImage = "/promotion-image.png",
    children
  } = props;

  const classes = useStyles();

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <Dialog className={classes.root} onClose={handleClose} open={isOpen}>
      <img className="dialogImage" src={promotionImage} />

      <div className="dialogContent">
        <div>
          <h3 className="heading">{title}</h3>
          <p className="description">{description}</p>
          {children && children}
        </div>

        <Button onClick={handleClose} color="primary" variant="contained" className="confirmButton">
          Confirm
        </Button>
      </div>
    </Dialog>
  );
};

export default PromotionDialog;
