import React, { useEffect, useState } from "react";
import {
  Alert,
  Button,
  createStyles,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Divider,
  Fab,
  LinearProgress,
  makeStyles,
  Slide,
  Theme,
  Typography,
} from "@material-ui/core";
import { TransitionProps } from "@material-ui/core/transitions";
import { useLocation } from "react-router-dom";
import ReactCodeInput from "react-code-input";

// src
import {
  auth0,
  getAuthMeta,
  storeAuthMeta,
  isPhoneNumberCached,
} from "../services/auth0";
import TOSString from "./TOSString";
import axios from "../services/backendAxios";

const SMS_CODE_LENGTH = 4;

const Transition = React.forwardRef(
  (
    props: TransitionProps & { children?: React.ReactElement<any, any> },
    ref: React.Ref<unknown>
  ) => {
    return <Slide direction="up" ref={ref} {...props} />;
  }
);
Transition.displayName = "SlideUpTransition";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      "& .MuiDialogTitle-root": {
        textAlign: "center",
      },
      "& .dlgContent": {
        display: "flex",
        alignItems: "center",
        flexDirection: "column",
        textAlign: "center",
        "& input[type=number]": {
          paddingRight: 0,
        },
        "& input[type=number]::-webkit-inner-spin-button": {
          WebkitAppearance: "none",
          margin: 0,
        },
        "& input[type=number]::-webkit-outer-spin-button": {
          WebkitAppearance: "none",
          margin: 0,
        },
      },
      "& .dlgActions": {
        display: "flex",
        justifyContent: "space-evenly",
        paddingTop: theme.spacing(2),
        paddingBottom: theme.spacing(1),
      },
      "& .MuiAlert-root": {
        backgroundColor: "transparent",
        padding: 0,
      },
      "& .TOSText": {
        paddingTop: 0,
      },
    },
  })
);

const PhoneVerificationDialog = (props: {
  phoneNumber: string;
  open: boolean;
  setOpen: (open: boolean) => void;
  redirectTo?: string;
}): React.ReactElement => {
  const classes = useStyles();
  const [loading, setLoading] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const [progress, setProgress] = useState(-1);
  const [hasConsent, setConsent] = useState(false);
  const [code, setCode] = useState("");
  const [codeInputKey, setCodeInputKey] = useState(0);
  const [isInvalidPhoneNumber, setIsInvalidPhoneNumber] = useState(false)

  const { phoneNumber: phoneNo, open, setOpen, redirectTo } = props;
  const phoneNumber = phoneNo.startsWith("+") ? phoneNo : `+${phoneNo}`;
  const location = useLocation();

  const kickOfftimer = () => {
    if (progress < 0) {
      setProgress(60);
      const timer = setInterval(() => {
        setProgress((prevProgress) => {
          if (prevProgress < 0) {
            clearInterval(timer);
          }
          return prevProgress - 1;
        });
      }, 1000);
    }
  };

  const sendSMSCode = () => {
    if (open) {

      const authMeta = getAuthMeta();
      const lastTimeSMS = authMeta?.lastTimeSMS || 0;
      const phoneHashArr = authMeta?.phoneHashArray || [];

      // handle user consent
      if (isPhoneNumberCached(phoneHashArr, phoneNumber)) {
        setConsent(true);
      } else {
        axios
          .get<boolean>("/users/exist", {
            params: { phoneNumber },
          })
          .then((response) => {
            if (response.status === 200 && response.data) {
              setConsent(true);
            }
            setConsent(false);
          });
      }

      // if one minute has passed
      if (progress <= 60) {
        setLoading(true);
        setCode("");
        setErrorMsg("");

        auth0.passwordlessStart(
          {
            connection: "sms",
            send: "code",
            phoneNumber,
          },
          (err, result) => {
            if (err) {
              if(err.statusCode === 400) {
                setIsInvalidPhoneNumber(true)
              }
              setLoading(false);
              // need to handle different error code
              setErrorMsg(err.description || err.error);
            } else {
              storeAuthMeta(Date.now());
              kickOfftimer();
              setLoading(false);
            }
          }
        );
      }
    }
  };

  const authorizeSMS = () => {
    if (code?.length === SMS_CODE_LENGTH) {
      // show drawer by default
      const state = redirectTo || location.pathname;
      auth0.passwordlessLogin(
        {
          connection: "sms",
          phoneNumber,
          verificationCode: code,
          state,
        },
        (err) => {
          if (err) {
            setLoading(false);
            setErrorMsg(err.error_description || err.error);
          } else {
            setLoading(false);
            setOpen(false);
          }
        }
      );
    }
  };

  const handleClose = () => {
    setOpen(false);
    setErrorMsg("");
    setCodeInputKey(codeInputKey + 1);
    setCode("");
    setProgress(-1)
  };

  const handleSignup = () => {
    setLoading(true);
    authorizeSMS();
  };

  const onCodeChange = (c: string) => {
    setCode(c);
  };

  useEffect(() => {
    sendSMSCode();
  }, [open]);

  useEffect(() => {
    if (hasConsent && code.length === SMS_CODE_LENGTH && !loading) {
      handleSignup();
    }
  }, [code]);

  return (
    <Dialog
      onClose={handleClose}
      aria-labelledby="Phone verfication"
      TransitionComponent={Transition}
      keepMounted
      open={open}
      className={classes.root}
    >
      <DialogTitle>Enter your verification code</DialogTitle>
      {loading && <LinearProgress />}

      <DialogContent className="dlgContent">
        <ReactCodeInput
          type="number"
          fields={SMS_CODE_LENGTH}
          autoFocus
          name="phone-verification-code"
          inputMode="numeric"
          value={code}
          key={codeInputKey}
          onChange={onCodeChange}
        />
        <Typography
          gutterBottom
          variant="subtitle2"
          color="textSecondary"
          component="div"
        >
          Code sent to {phoneNumber}
          {errorMsg && <Alert severity="error">{errorMsg}</Alert>}
        </Typography>
      </DialogContent>
      <Divider />
      <DialogActions className="dlgActions">
        <Fab
          onClick={sendSMSCode}
          color="secondary"
          variant="extended"
          size="medium"
          disabled={loading || progress > 0 || isInvalidPhoneNumber}
        >
          {progress > 0 ? `Resend (:${progress})` : `Resend`}
        </Fab>
        <Fab
          onClick={handleSignup}
          color="primary"
          variant="extended"
          size="medium"
          disabled={loading || code.length !== SMS_CODE_LENGTH}
        >
          Continue
        </Fab>
      </DialogActions>
      {!hasConsent && (
        <DialogContent className="TOSText">
          <TOSString />
        </DialogContent>
      )}
    </Dialog>
  );
};

export default PhoneVerificationDialog;
